'use client'

import { useState, useRef, useEffect } from 'react'
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card'
import { Button } from '@/components/ui/button'
import { Badge } from '@/components/ui/badge'
import { Alert, AlertDescription } from '@/components/ui/alert'
import { Tabs, TabsContent, TabsList, TabsTrigger } from '@/components/ui/tabs'

interface CameraViewProps {
  deviceId: string
  isConnected: boolean
}

export default function CameraView({ deviceId, isConnected }: CameraViewProps) {
  const [activeCamera, setActiveCamera] = useState<'front' | 'back'>('back')
  const [isStreaming, setIsStreaming] = useState(false)
  const [flashEnabled, setFlashEnabled] = useState(false)
  const [error, setError] = useState('')
  const [connectionStatus, setConnectionStatus] = useState<'idle' | 'connecting' | 'streaming' | 'error'>('idle')
  const [capturedPhotos, setCapturedPhotos] = useState<string[]>([])
  const videoRef = useRef<HTMLVideoElement>(null)
  const canvasRef = useRef<HTMLCanvasElement>(null)

  useEffect(() => {
    return () => {
      stopCameraStream()
    }
  }, [])

  const startCameraStream = async (camera: 'front' | 'back') => {
    if (!isConnected) {
      setError('Cihaz bağlı değil')
      return
    }

    setError('')
    setConnectionStatus('connecting')
    setActiveCamera(camera)

    try {
      // Simüle kamera akışı başlatma
      await new Promise(resolve => setTimeout(resolve, 1500))
      
      setIsStreaming(true)
      setConnectionStatus('streaming')
      
      // Simüle video içeriği
      simulateCameraFeed()
      
    } catch (err) {
      setError('Kamera akışı başlatılamadı')
      setConnectionStatus('error')
    }
  }

  const stopCameraStream = () => {
    setIsStreaming(false)
    setConnectionStatus('idle')
    
    const video = videoRef.current
    if (video && video.srcObject) {
      const stream = video.srcObject as MediaStream
      stream.getTracks().forEach(track => track.stop())
      video.srcObject = null
    }
  }

  const simulateCameraFeed = () => {
    const canvas = canvasRef.current
    if (!canvas) return

    const ctx = canvas.getContext('2d')
    if (!ctx) return

    const animate = () => {
      if (!isStreaming) return

      // Simüle kamera görüntüsü
      const gradient = ctx.createLinearGradient(0, 0, canvas.width, canvas.height)
      gradient.addColorStop(0, activeCamera === 'front' ? '#4f46e5' : '#059669')
      gradient.addColorStop(1, activeCamera === 'front' ? '#7c3aed' : '#0d9488')
      
      ctx.fillStyle = gradient
      ctx.fillRect(0, 0, canvas.width, canvas.height)
      
      // Kamera türü göstergesi
      ctx.fillStyle = 'rgba(255, 255, 255, 0.9)'
      ctx.font = '20px Arial'
      ctx.fillText(
        activeCamera === 'front' ? 'Ön Kamera' : 'Arka Kamera', 
        20, 
        40
      )
      
      // Zaman damgası
      ctx.font = '14px Arial'
      ctx.fillText(
        new Date().toLocaleTimeString('tr-TR'), 
        20, 
        canvas.height - 20
      )
      
      // Flash göstergesi
      if (flashEnabled) {
        ctx.fillStyle = 'rgba(255, 255, 0, 0.3)'
        ctx.fillRect(0, 0, canvas.width, canvas.height)
      }

      requestAnimationFrame(animate)
    }
    
    animate()
  }

  const switchCamera = () => {
    const newCamera = activeCamera === 'front' ? 'back' : 'front'
    if (isStreaming) {
      stopCameraStream()
      setTimeout(() => startCameraStream(newCamera), 500)
    } else {
      setActiveCamera(newCamera)
    }
  }

  const toggleFlash = () => {
    if (activeCamera === 'front') {
      setError('Ön kamerada flaş kullanılamaz')
      return
    }
    setFlashEnabled(!flashEnabled)
  }

  const capturePhoto = async () => {
    if (!isStreaming) {
      setError('Kamera akışı aktif değil')
      return
    }

    try {
      const canvas = canvasRef.current
      if (!canvas) return

      // Canvas'tan fotoğraf yakala
      const dataURL = canvas.toDataURL('image/jpeg', 0.9)
      
      // Fotoğrafı listeye ekle
      setCapturedPhotos(prev => [dataURL, ...prev])
      
      // Flash efekti
      if (flashEnabled) {
        const flashOverlay = document.createElement('div')
        flashOverlay.style.position = 'fixed'
        flashOverlay.style.top = '0'
        flashOverlay.style.left = '0'
        flashOverlay.style.width = '100%'
        flashOverlay.style.height = '100%'
        flashOverlay.style.backgroundColor = 'white'
        flashOverlay.style.opacity = '0.8'
        flashOverlay.style.zIndex = '9999'
        flashOverlay.style.pointerEvents = 'none'
        
        document.body.appendChild(flashOverlay)
        
        setTimeout(() => {
          document.body.removeChild(flashOverlay)
        }, 200)
      }
      
    } catch (err) {
      setError('Fotoğraf çekilemedi')
    }
  }

  const downloadPhoto = (photoUrl: string, index: number) => {
    const link = document.createElement('a')
    link.download = `photo-${Date.now()}-${index}.jpg`
    link.href = photoUrl
    link.click()
  }

  const deletePhoto = (index: number) => {
    setCapturedPhotos(prev => prev.filter((_, i) => i !== index))
  }

  const getStatusColor = () => {
    switch (connectionStatus) {
      case 'streaming': return 'bg-green-500'
      case 'connecting': return 'bg-yellow-500'
      case 'error': return 'bg-red-500'
      default: return 'bg-gray-500'
    }
  }

  const getStatusText = () => {
    switch (connectionStatus) {
      case 'streaming': return 'Kamera Aktif'
      case 'connecting': return 'Bağlanıyor'
      case 'error': return 'Hata'
      default: return 'Beklemede'
    }
  }

  return (
    <div className="space-y-6">
      <Card>
        <CardHeader>
          <div className="flex items-center justify-between">
            <div>
              <CardTitle>Kamera Kontrolü</CardTitle>
              <CardDescription>
                Cihazınızın kameralarını kontrol edin ve fotoğraf çekin
              </CardDescription>
            </div>
            <Badge className={getStatusColor()}>
              {getStatusText()}
            </Badge>
          </div>
        </CardHeader>
        <CardContent className="space-y-4">
          {/* Kontrol Butonları */}
          <div className="flex flex-wrap gap-2">
            {!isStreaming ? (
              <>
                <Button 
                  onClick={() => startCameraStream('back')}
                  disabled={!isConnected || connectionStatus === 'connecting'}
                >
                  {connectionStatus === 'connecting' ? 'Başlatılıyor...' : 'Arka Kamera'}
                </Button>
                <Button 
                  onClick={() => startCameraStream('front')}
                  disabled={!isConnected || connectionStatus === 'connecting'}
                  variant="outline"
                >
                  Ön Kamera
                </Button>
              </>
            ) : (
              <>
                <Button onClick={stopCameraStream} variant="destructive">
                  Kamerayı Kapat
                </Button>
                <Button onClick={switchCamera} variant="outline">
                  Kamera Değiştir
                </Button>
                <Button onClick={capturePhoto}>
                  Fotoğraf Çek
                </Button>
                {activeCamera === 'back' && (
                  <Button 
                    onClick={toggleFlash}
                    variant={flashEnabled ? 'default' : 'outline'}
                  >
                    Flaş {flashEnabled ? 'Açık' : 'Kapalı'}
                  </Button>
                )}
              </>
            )}
          </div>

          {error && (
            <Alert>
              <AlertDescription>{error}</AlertDescription>
            </Alert>
          )}
        </CardContent>
      </Card>

      <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
        {/* Kamera Görüntüsü */}
        <Card>
          <CardHeader>
            <CardTitle>Canlı Görüntü</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="relative">
              <canvas
                ref={canvasRef}
                width={320}
                height={240}
                className="w-full border rounded-lg bg-black"
                style={{ aspectRatio: '4/3' }}
              />
              {!isStreaming && (
                <div className="absolute inset-0 flex items-center justify-center bg-gray-100 rounded-lg">
                  <div className="text-center">
                    <p className="text-lg font-medium text-gray-600 mb-2">
                      Kamera Kapalı
                    </p>
                    <p className="text-sm text-gray-500">
                      Kamerayı açmak için yukarıdaki butonları kullanın
                    </p>
                  </div>
                </div>
              )}
              
              {isStreaming && (
                <div className="absolute top-2 left-2">
                  <Badge variant="secondary">
                    {activeCamera === 'front' ? 'Ön' : 'Arka'} Kamera
                  </Badge>
                </div>
              )}
            </div>
          </CardContent>
        </Card>

        {/* Çekilen Fotoğraflar */}
        <Card>
          <CardHeader>
            <CardTitle>Çekilen Fotoğraflar ({capturedPhotos.length})</CardTitle>
          </CardHeader>
          <CardContent>
            {capturedPhotos.length === 0 ? (
              <div className="text-center py-8">
                <p className="text-muted-foreground">
                  Henüz fotoğraf çekilmedi
                </p>
              </div>
            ) : (
              <div className="space-y-3 max-h-96 overflow-y-auto">
                {capturedPhotos.map((photo, index) => (
                  <div key={index} className="flex items-center gap-3 p-2 border rounded">
                    <img 
                      src={photo} 
                      alt={`Fotoğraf ${index + 1}`}
                      className="w-16 h-12 object-cover rounded"
                    />
                    <div className="flex-1">
                      <p className="text-sm font-medium">Fotoğraf {index + 1}</p>
                      <p className="text-xs text-muted-foreground">
                        {new Date().toLocaleString('tr-TR')}
                      </p>
                    </div>
                    <div className="flex gap-1">
                      <Button
                        size="sm"
                        variant="outline"
                        onClick={() => downloadPhoto(photo, index)}
                      >
                        İndir
                      </Button>
                      <Button
                        size="sm"
                        variant="outline"
                        onClick={() => deletePhoto(index)}
                      >
                        Sil
                      </Button>
                    </div>
                  </div>
                ))}
              </div>
            )}
          </CardContent>
        </Card>
      </div>
    </div>
  )
}
