'use client'

import { useState, useEffect } from 'react'
import { useParams } from 'next/navigation'
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card'
import { Button } from '@/components/ui/button'
import { Badge } from '@/components/ui/badge'
import { Tabs, TabsContent, TabsList, TabsTrigger } from '@/components/ui/tabs'
import { Alert, AlertDescription } from '@/components/ui/alert'

// Bileşenleri import et
import ScreenMonitor from '@/components/device-control/ScreenMonitor'
import CameraView from '@/components/device-control/CameraView'
import LocationMap from '@/components/device-control/LocationMap'
import SMSManager from '@/components/device-control/SMSManager'
import ContactsManager from '@/components/device-control/ContactsManager'
import GalleryViewer from '@/components/device-control/GalleryViewer'
import DeviceWipe from '@/components/device-control/DeviceWipe'

interface Device {
  id: string
  name: string
  ip: string
  status: 'connected' | 'disconnected' | 'connecting'
  lastSeen: string
  batteryLevel?: number
  storageUsed?: number
  storageTotal?: number
}

export default function DeviceControlPage() {
  const params = useParams()
  const deviceId = params.deviceId as string
  
  const [device, setDevice] = useState<Device | null>(null)
  const [isLoading, setIsLoading] = useState(true)
  const [error, setError] = useState('')
  const [activeTab, setActiveTab] = useState('screen')

  // Simüle cihaz bilgilerini yükle
  useEffect(() => {
    const loadDeviceInfo = async () => {
      setIsLoading(true)
      try {
        // Simüle cihaz bilgisi yükleme
        await new Promise(resolve => setTimeout(resolve, 1000))
        
        const mockDevice: Device = {
          id: deviceId,
          name: 'Android Cihaz',
          ip: '192.168.1.100',
          status: 'connected',
          lastSeen: new Date().toLocaleString('tr-TR'),
          batteryLevel: Math.floor(Math.random() * 100),
          storageUsed: Math.floor(Math.random() * 50) + 20,
          storageTotal: 64
        }
        
        setDevice(mockDevice)
      } catch (err) {
        setError('Cihaz bilgileri yüklenemedi')
      } finally {
        setIsLoading(false)
      }
    }

    if (deviceId) {
      loadDeviceInfo()
    }
  }, [deviceId])

  const handleDisconnect = () => {
    if (device) {
      setDevice({ ...device, status: 'disconnected' })
    }
  }

  const handleReconnect = async () => {
    if (device) {
      setDevice({ ...device, status: 'connecting' })
      
      // Simüle yeniden bağlanma
      setTimeout(() => {
        setDevice(prev => prev ? { ...prev, status: 'connected', lastSeen: new Date().toLocaleString('tr-TR') } : null)
      }, 2000)
    }
  }

  const getStatusColor = (status: string) => {
    switch (status) {
      case 'connected': return 'bg-green-500'
      case 'connecting': return 'bg-yellow-500'
      case 'disconnected': return 'bg-red-500'
      default: return 'bg-gray-500'
    }
  }

  const getStatusText = (status: string) => {
    switch (status) {
      case 'connected': return 'Bağlı'
      case 'connecting': return 'Bağlanıyor'
      case 'disconnected': return 'Bağlantı Kesildi'
      default: return 'Bilinmiyor'
    }
  }

  if (isLoading) {
    return (
      <div className="container mx-auto p-6 max-w-6xl">
        <div className="flex items-center justify-center min-h-[400px]">
          <div className="text-center">
            <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-primary mx-auto mb-4"></div>
            <p className="text-muted-foreground">Cihaz bilgileri yükleniyor...</p>
          </div>
        </div>
      </div>
    )
  }

  if (error || !device) {
    return (
      <div className="container mx-auto p-6 max-w-6xl">
        <Alert>
          <AlertDescription>
            {error || 'Cihaz bulunamadı'}
          </AlertDescription>
        </Alert>
      </div>
    )
  }

  return (
    <div className="container mx-auto p-6 max-w-7xl">
      {/* Cihaz Başlığı ve Durum */}
      <div className="mb-6">
        <div className="flex items-center justify-between mb-4">
          <div>
            <h1 className="text-3xl font-bold">{device.name}</h1>
            <p className="text-muted-foreground">IP: {device.ip}</p>
          </div>
          
          <div className="flex items-center gap-4">
            <Badge className={getStatusColor(device.status)}>
              {getStatusText(device.status)}
            </Badge>
            
            {device.status === 'connected' ? (
              <Button variant="outline" onClick={handleDisconnect}>
                Bağlantıyı Kes
              </Button>
            ) : (
              <Button onClick={handleReconnect} disabled={device.status === 'connecting'}>
                {device.status === 'connecting' ? 'Bağlanıyor...' : 'Yeniden Bağlan'}
              </Button>
            )}
          </div>
        </div>

        {/* Cihaz İstatistikleri */}
        <div className="grid grid-cols-1 md:grid-cols-4 gap-4">
          <Card>
            <CardContent className="p-4">
              <div className="flex items-center justify-between">
                <div>
                  <p className="text-sm text-muted-foreground">Batarya</p>
                  <p className="text-2xl font-bold">{device.batteryLevel}%</p>
                </div>
                <div className="w-12 h-12 rounded-full bg-green-100 flex items-center justify-center">
                  <div className="w-6 h-8 border-2 border-green-600 rounded-sm relative">
                    <div 
                      className="absolute bottom-0 left-0 right-0 bg-green-600 rounded-sm"
                      style={{ height: `${device.batteryLevel}%` }}
                    ></div>
                    <div className="absolute -top-1 left-1/2 transform -translate-x-1/2 w-1 h-1 bg-green-600 rounded-sm"></div>
                  </div>
                </div>
              </div>
            </CardContent>
          </Card>

          <Card>
            <CardContent className="p-4">
              <div className="flex items-center justify-between">
                <div>
                  <p className="text-sm text-muted-foreground">Depolama</p>
                  <p className="text-2xl font-bold">{device.storageUsed}GB</p>
                  <p className="text-xs text-muted-foreground">/ {device.storageTotal}GB</p>
                </div>
                <div className="w-12 h-12 rounded-full bg-blue-100 flex items-center justify-center">
                  <div className="w-8 h-8 rounded-full border-4 border-blue-200 relative">
                    <div 
                      className="absolute inset-0 rounded-full border-4 border-blue-600"
                      style={{ 
                        clipPath: `polygon(50% 50%, 50% 0%, ${50 + (device.storageUsed! / device.storageTotal!) * 50}% 0%, 100% 100%, 0% 100%)` 
                      }}
                    ></div>
                  </div>
                </div>
              </div>
            </CardContent>
          </Card>

          <Card>
            <CardContent className="p-4">
              <div className="flex items-center justify-between">
                <div>
                  <p className="text-sm text-muted-foreground">Durum</p>
                  <p className="text-lg font-bold">{getStatusText(device.status)}</p>
                </div>
                <div className={`w-4 h-4 rounded-full ${getStatusColor(device.status)}`}></div>
              </div>
            </CardContent>
          </Card>

          <Card>
            <CardContent className="p-4">
              <div>
                <p className="text-sm text-muted-foreground">Son Görülme</p>
                <p className="text-sm font-medium">{device.lastSeen}</p>
              </div>
            </CardContent>
          </Card>
        </div>
      </div>

      {/* Ana Kontrol Paneli */}
      <Card>
        <CardContent className="p-6">
          <Tabs value={activeTab} onValueChange={setActiveTab} className="w-full">
            <TabsList className="grid w-full grid-cols-7">
              <TabsTrigger value="screen">Ekran</TabsTrigger>
              <TabsTrigger value="camera">Kamera</TabsTrigger>
              <TabsTrigger value="location">Konum</TabsTrigger>
              <TabsTrigger value="messages">Mesajlar</TabsTrigger>
              <TabsTrigger value="contacts">Kişiler</TabsTrigger>
              <TabsTrigger value="gallery">Galeri</TabsTrigger>
              <TabsTrigger value="wipe">Sıfırlama</TabsTrigger>
            </TabsList>

            <div className="mt-6">
              <TabsContent value="screen" className="space-y-4">
                <div className="mb-4">
                  <h3 className="text-lg font-semibold">Ekran Kontrolü</h3>
                  <p className="text-muted-foreground">
                    Cihazınızın ekranını canlı olarak izleyin ve uzaktan kontrol edin
                  </p>
                </div>
                <ScreenMonitor 
                  deviceId={deviceId} 
                  isConnected={device.status === 'connected'} 
                />
              </TabsContent>

              <TabsContent value="camera" className="space-y-4">
                <div className="mb-4">
                  <h3 className="text-lg font-semibold">Kamera Kontrolü</h3>
                  <p className="text-muted-foreground">
                    Ön ve arka kamerayı kontrol edin, fotoğraf çekin
                  </p>
                </div>
                <CameraView 
                  deviceId={deviceId} 
                  isConnected={device.status === 'connected'} 
                />
              </TabsContent>

              <TabsContent value="location" className="space-y-4">
                <div className="mb-4">
                  <h3 className="text-lg font-semibold">Konum Takibi</h3>
                  <p className="text-muted-foreground">
                    Cihazınızın konumunu gerçek zamanlı olarak takip edin
                  </p>
                </div>
                <LocationMap 
                  deviceId={deviceId} 
                  isConnected={device.status === 'connected'} 
                />
              </TabsContent>

              <TabsContent value="messages" className="space-y-4">
                <div className="mb-4">
                  <h3 className="text-lg font-semibold">SMS Yönetimi</h3>
                  <p className="text-muted-foreground">
                    SMS mesajlarını görüntüleyin, gönderin ve yönetin
                  </p>
                </div>
                <SMSManager 
                  deviceId={deviceId} 
                  isConnected={device.status === 'connected'} 
                />
              </TabsContent>

              <TabsContent value="contacts" className="space-y-4">
                <div className="mb-4">
                  <h3 className="text-lg font-semibold">Kişiler Yönetimi</h3>
                  <p className="text-muted-foreground">
                    Telefon rehberinizdeki kişileri görüntüleyin ve yönetin
                  </p>
                </div>
                <ContactsManager 
                  deviceId={deviceId} 
                  isConnected={device.status === 'connected'} 
                />
              </TabsContent>

              <TabsContent value="gallery" className="space-y-4">
                <div className="mb-4">
                  <h3 className="text-lg font-semibold">Galeri Yönetimi</h3>
                  <p className="text-muted-foreground">
                    Cihazınızdaki fotoğraf ve videoları görüntüleyin, indirin ve yönetin
                  </p>
                </div>
                <GalleryViewer 
                  deviceId={deviceId} 
                  isConnected={device.status === 'connected'} 
                />
              </TabsContent>

              <TabsContent value="wipe" className="space-y-4">
                <div className="mb-4">
                  <h3 className="text-lg font-semibold">Cihaz Sıfırlama</h3>
                  <p className="text-muted-foreground">
                    Cihazınızı temizleyin veya fabrika ayarlarına sıfırlayın
                  </p>
                </div>
                <DeviceWipe 
                  deviceId={deviceId} 
                  isConnected={device.status === 'connected'} 
                />
              </TabsContent>
            </div>
          </Tabs>
        </CardContent>
      </Card>

      {/* Bağlantı Durumu Uyarısı */}
      {device.status !== 'connected' && (
        <Alert className="mt-6">
          <AlertDescription>
            {device.status === 'connecting' 
              ? 'Cihaza bağlanılıyor, lütfen bekleyin...'
              : 'Cihaz bağlı değil. Özellikleri kullanmak için cihazı yeniden bağlayın.'
            }
          </AlertDescription>
        </Alert>
      )}
    </div>
  )
}
