'use client'

import { useState, useEffect } from 'react'
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card'
import { Button } from '@/components/ui/button'
import { Input } from '@/components/ui/input'
import { Label } from '@/components/ui/label'
import { Badge } from '@/components/ui/badge'
import { Alert, AlertDescription } from '@/components/ui/alert'
import { Dialog, DialogContent, DialogDescription, DialogHeader, DialogTitle, DialogTrigger } from '@/components/ui/dialog'
import { Tabs, TabsContent, TabsList, TabsTrigger } from '@/components/ui/tabs'

interface Contact {
  id: string
  name: string
  phoneNumbers: { type: string; number: string }[]
  emails: { type: string; email: string }[]
  organization?: string
  notes?: string
  favorite: boolean
  lastModified: string
}

interface ContactsManagerProps {
  deviceId: string
  isConnected: boolean
}

export default function ContactsManager({ deviceId, isConnected }: ContactsManagerProps) {
  const [contacts, setContacts] = useState<Contact[]>([])
  const [isLoading, setIsLoading] = useState(false)
  const [error, setError] = useState('')
  const [searchTerm, setSearchTerm] = useState('')
  const [selectedContact, setSelectedContact] = useState<Contact | null>(null)
  const [isEditing, setIsEditing] = useState(false)
  const [editingContact, setEditingContact] = useState<Partial<Contact>>({})

  // Simüle kişi verileri
  const generateSampleContacts = (): Contact[] => {
    return [
      {
        id: '1',
        name: 'Ahmet Yılmaz',
        phoneNumbers: [
          { type: 'Mobil', number: '+905551234567' },
          { type: 'İş', number: '+902121234567' }
        ],
        emails: [
          { type: 'Kişisel', email: 'ahmet@email.com' },
          { type: 'İş', email: 'ahmet@sirket.com' }
        ],
        organization: 'ABC Şirketi',
        notes: 'Proje yöneticisi',
        favorite: true,
        lastModified: new Date(Date.now() - 86400000).toLocaleString('tr-TR')
      },
      {
        id: '2',
        name: 'Fatma Demir',
        phoneNumbers: [
          { type: 'Mobil', number: '+905559876543' }
        ],
        emails: [
          { type: 'Kişisel', email: 'fatma@email.com' }
        ],
        organization: 'XYZ Ltd.',
        favorite: false,
        lastModified: new Date(Date.now() - 172800000).toLocaleString('tr-TR')
      },
      {
        id: '3',
        name: 'Mehmet Kaya',
        phoneNumbers: [
          { type: 'Mobil', number: '+905551111111' },
          { type: 'Ev', number: '+902161111111' }
        ],
        emails: [],
        favorite: false,
        lastModified: new Date(Date.now() - 259200000).toLocaleString('tr-TR')
      },
      {
        id: '4',
        name: 'Ayşe Özkan',
        phoneNumbers: [
          { type: 'Mobil', number: '+905552222222' }
        ],
        emails: [
          { type: 'Kişisel', email: 'ayse@email.com' }
        ],
        organization: 'Freelancer',
        notes: 'Grafik tasarımcı',
        favorite: true,
        lastModified: new Date(Date.now() - 345600000).toLocaleString('tr-TR')
      }
    ]
  }

  const loadContacts = async () => {
    if (!isConnected) {
      setError('Cihaz bağlı değil')
      return
    }

    setIsLoading(true)
    setError('')

    try {
      // Simüle kişi yükleme
      await new Promise(resolve => setTimeout(resolve, 1500))
      
      const sampleContacts = generateSampleContacts()
      setContacts(sampleContacts)
      
    } catch (err) {
      setError('Kişiler yüklenemedi')
    } finally {
      setIsLoading(false)
    }
  }

  const saveContact = async () => {
    if (!editingContact.name?.trim()) {
      setError('Kişi adı gereklidir')
      return
    }

    try {
      // Simüle kişi kaydetme
      await new Promise(resolve => setTimeout(resolve, 1000))
      
      if (editingContact.id) {
        // Güncelleme
        setContacts(prev => prev.map(contact => 
          contact.id === editingContact.id 
            ? { ...contact, ...editingContact, lastModified: new Date().toLocaleString('tr-TR') }
            : contact
        ))
      } else {
        // Yeni kişi
        const newContact: Contact = {
          id: Date.now().toString(),
          name: editingContact.name,
          phoneNumbers: editingContact.phoneNumbers || [],
          emails: editingContact.emails || [],
          organization: editingContact.organization,
          notes: editingContact.notes,
          favorite: editingContact.favorite || false,
          lastModified: new Date().toLocaleString('tr-TR')
        }
        setContacts(prev => [newContact, ...prev])
      }
      
      setIsEditing(false)
      setEditingContact({})
      setError('')
      
    } catch (err) {
      setError('Kişi kaydedilemedi')
    }
  }

  const deleteContact = async (contactId: string) => {
    try {
      // Simüle kişi silme
      await new Promise(resolve => setTimeout(resolve, 500))
      
      setContacts(prev => prev.filter(contact => contact.id !== contactId))
      setSelectedContact(null)
      
    } catch (err) {
      setError('Kişi silinemedi')
    }
  }

  const toggleFavorite = async (contactId: string) => {
    try {
      setContacts(prev => prev.map(contact => 
        contact.id === contactId 
          ? { ...contact, favorite: !contact.favorite }
          : contact
      ))
    } catch (err) {
      setError('Favori durumu güncellenemedi')
    }
  }

  const startEdit = (contact?: Contact) => {
    if (contact) {
      setEditingContact(contact)
    } else {
      setEditingContact({
        name: '',
        phoneNumbers: [{ type: 'Mobil', number: '' }],
        emails: [{ type: 'Kişisel', email: '' }],
        organization: '',
        notes: '',
        favorite: false
      })
    }
    setIsEditing(true)
  }

  const addPhoneNumber = () => {
    setEditingContact(prev => ({
      ...prev,
      phoneNumbers: [...(prev.phoneNumbers || []), { type: 'Mobil', number: '' }]
    }))
  }

  const addEmail = () => {
    setEditingContact(prev => ({
      ...prev,
      emails: [...(prev.emails || []), { type: 'Kişisel', email: '' }]
    }))
  }

  const updatePhoneNumber = (index: number, field: 'type' | 'number', value: string) => {
    setEditingContact(prev => ({
      ...prev,
      phoneNumbers: prev.phoneNumbers?.map((phone, i) => 
        i === index ? { ...phone, [field]: value } : phone
      )
    }))
  }

  const updateEmail = (index: number, field: 'type' | 'email', value: string) => {
    setEditingContact(prev => ({
      ...prev,
      emails: prev.emails?.map((email, i) => 
        i === index ? { ...email, [field]: value } : email
      )
    }))
  }

  const removePhoneNumber = (index: number) => {
    setEditingContact(prev => ({
      ...prev,
      phoneNumbers: prev.phoneNumbers?.filter((_, i) => i !== index)
    }))
  }

  const removeEmail = (index: number) => {
    setEditingContact(prev => ({
      ...prev,
      emails: prev.emails?.filter((_, i) => i !== index)
    }))
  }

  const filteredContacts = contacts.filter(contact => 
    contact.name.toLowerCase().includes(searchTerm.toLowerCase()) ||
    contact.phoneNumbers.some(phone => phone.number.includes(searchTerm)) ||
    contact.emails.some(email => email.email.toLowerCase().includes(searchTerm.toLowerCase())) ||
    (contact.organization && contact.organization.toLowerCase().includes(searchTerm.toLowerCase()))
  )

  const favoriteContacts = filteredContacts.filter(contact => contact.favorite)
  const regularContacts = filteredContacts.filter(contact => !contact.favorite)

  useEffect(() => {
    if (isConnected && contacts.length === 0) {
      loadContacts()
    }
  }, [isConnected])

  return (
    <div className="space-y-6">
      <Card>
        <CardHeader>
          <CardTitle>Kişiler Yönetimi</CardTitle>
          <CardDescription>
            Telefon rehberinizdeki kişileri görüntüleyin ve yönetin
          </CardDescription>
        </CardHeader>
        <CardContent className="space-y-4">
          {/* Kontrol Butonları */}
          <div className="flex flex-wrap gap-2">
            <Button 
              onClick={loadContacts}
              disabled={!isConnected || isLoading}
            >
              {isLoading ? 'Yükleniyor...' : 'Kişileri Yenile'}
            </Button>
            
            <Dialog open={isEditing} onOpenChange={setIsEditing}>
              <DialogTrigger asChild>
                <Button variant="outline" disabled={!isConnected} onClick={() => startEdit()}>
                  Yeni Kişi Ekle
                </Button>
              </DialogTrigger>
              <DialogContent className="max-w-2xl max-h-[80vh] overflow-y-auto">
                <DialogHeader>
                  <DialogTitle>
                    {editingContact.id ? 'Kişi Düzenle' : 'Yeni Kişi Ekle'}
                  </DialogTitle>
                  <DialogDescription>
                    Kişi bilgilerini girin veya düzenleyin
                  </DialogDescription>
                </DialogHeader>
                
                <div className="space-y-4">
                  {/* Temel Bilgiler */}
                  <div className="space-y-2">
                    <Label htmlFor="name">Ad Soyad *</Label>
                    <Input
                      id="name"
                      value={editingContact.name || ''}
                      onChange={(e) => setEditingContact(prev => ({ ...prev, name: e.target.value }))}
                      placeholder="Kişi adını girin"
                    />
                  </div>
                  
                  <div className="space-y-2">
                    <Label htmlFor="organization">Organizasyon</Label>
                    <Input
                      id="organization"
                      value={editingContact.organization || ''}
                      onChange={(e) => setEditingContact(prev => ({ ...prev, organization: e.target.value }))}
                      placeholder="Şirket veya organizasyon"
                    />
                  </div>

                  {/* Telefon Numaraları */}
                  <div className="space-y-2">
                    <div className="flex items-center justify-between">
                      <Label>Telefon Numaraları</Label>
                      <Button size="sm" variant="outline" onClick={addPhoneNumber}>
                        Numara Ekle
                      </Button>
                    </div>
                    {editingContact.phoneNumbers?.map((phone, index) => (
                      <div key={index} className="flex gap-2">
                        <Input
                          placeholder="Tür"
                          value={phone.type}
                          onChange={(e) => updatePhoneNumber(index, 'type', e.target.value)}
                          className="w-24"
                        />
                        <Input
                          placeholder="+905551234567"
                          value={phone.number}
                          onChange={(e) => updatePhoneNumber(index, 'number', e.target.value)}
                          className="flex-1"
                        />
                        <Button 
                          size="sm" 
                          variant="outline" 
                          onClick={() => removePhoneNumber(index)}
                        >
                          Sil
                        </Button>
                      </div>
                    ))}
                  </div>

                  {/* E-posta Adresleri */}
                  <div className="space-y-2">
                    <div className="flex items-center justify-between">
                      <Label>E-posta Adresleri</Label>
                      <Button size="sm" variant="outline" onClick={addEmail}>
                        E-posta Ekle
                      </Button>
                    </div>
                    {editingContact.emails?.map((email, index) => (
                      <div key={index} className="flex gap-2">
                        <Input
                          placeholder="Tür"
                          value={email.type}
                          onChange={(e) => updateEmail(index, 'type', e.target.value)}
                          className="w-24"
                        />
                        <Input
                          placeholder="email@example.com"
                          value={email.email}
                          onChange={(e) => updateEmail(index, 'email', e.target.value)}
                          className="flex-1"
                        />
                        <Button 
                          size="sm" 
                          variant="outline" 
                          onClick={() => removeEmail(index)}
                        >
                          Sil
                        </Button>
                      </div>
                    ))}
                  </div>

                  {/* Notlar */}
                  <div className="space-y-2">
                    <Label htmlFor="notes">Notlar</Label>
                    <Input
                      id="notes"
                      value={editingContact.notes || ''}
                      onChange={(e) => setEditingContact(prev => ({ ...prev, notes: e.target.value }))}
                      placeholder="Kişi hakkında notlar"
                    />
                  </div>

                  {/* Favori */}
                  <div className="flex items-center space-x-2">
                    <input
                      type="checkbox"
                      id="favorite"
                      checked={editingContact.favorite || false}
                      onChange={(e) => setEditingContact(prev => ({ ...prev, favorite: e.target.checked }))}
                    />
                    <Label htmlFor="favorite">Favorilere ekle</Label>
                  </div>

                  <div className="flex gap-2">
                    <Button onClick={saveContact} className="flex-1">
                      {editingContact.id ? 'Güncelle' : 'Kaydet'}
                    </Button>
                    <Button variant="outline" onClick={() => setIsEditing(false)}>
                      İptal
                    </Button>
                  </div>
                </div>
              </DialogContent>
            </Dialog>
          </div>

          {error && (
            <Alert>
              <AlertDescription>{error}</AlertDescription>
            </Alert>
          )}

          {/* Arama */}
          <div className="space-y-2">
            <Label htmlFor="search">Kişilerde Ara</Label>
            <Input
              id="search"
              placeholder="Ad, telefon, e-posta veya organizasyon..."
              value={searchTerm}
              onChange={(e) => setSearchTerm(e.target.value)}
            />
          </div>
        </CardContent>
      </Card>

      {/* Kişi Listesi */}
      <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
        {/* Kişi Listesi */}
        <Card>
          <CardHeader>
            <CardTitle>
              Kişiler ({filteredContacts.length})
              {favoriteContacts.length > 0 && (
                <Badge variant="secondary" className="ml-2">
                  {favoriteContacts.length} Favori
                </Badge>
              )}
            </CardTitle>
          </CardHeader>
          <CardContent>
            {filteredContacts.length === 0 ? (
              <div className="text-center py-8">
                <p className="text-muted-foreground">
                  {contacts.length === 0 ? 'Henüz kişi yok' : 'Arama sonucu bulunamadı'}
                </p>
              </div>
            ) : (
              <div className="space-y-2 max-h-96 overflow-y-auto">
                {/* Favori Kişiler */}
                {favoriteContacts.length > 0 && (
                  <>
                    <h4 className="font-medium text-sm text-muted-foreground">Favoriler</h4>
                    {favoriteContacts.map((contact) => (
                      <div 
                        key={contact.id}
                        className="p-3 border rounded-lg cursor-pointer hover:bg-muted/50"
                        onClick={() => setSelectedContact(contact)}
                      >
                        <div className="flex items-center justify-between mb-2">
                          <h4 className="font-medium">{contact.name}</h4>
                          <div className="flex items-center gap-1">
                            <Badge variant="secondary">★</Badge>
                            <Button
                              size="sm"
                              variant="outline"
                              onClick={(e) => {
                                e.stopPropagation()
                                startEdit(contact)
                              }}
                            >
                              Düzenle
                            </Button>
                          </div>
                        </div>
                        
                        {contact.phoneNumbers.length > 0 && (
                          <p className="text-sm text-muted-foreground">
                            {contact.phoneNumbers[0].number}
                          </p>
                        )}
                        
                        {contact.organization && (
                          <p className="text-xs text-muted-foreground">
                            {contact.organization}
                          </p>
                        )}
                      </div>
                    ))}
                    
                    {regularContacts.length > 0 && (
                      <div className="my-4">
                        <h4 className="font-medium text-sm text-muted-foreground">Diğer Kişiler</h4>
                      </div>
                    )}
                  </>
                )}
                
                {/* Diğer Kişiler */}
                {regularContacts.map((contact) => (
                  <div 
                    key={contact.id}
                    className="p-3 border rounded-lg cursor-pointer hover:bg-muted/50"
                    onClick={() => setSelectedContact(contact)}
                  >
                    <div className="flex items-center justify-between mb-2">
                      <h4 className="font-medium">{contact.name}</h4>
                      <Button
                        size="sm"
                        variant="outline"
                        onClick={(e) => {
                          e.stopPropagation()
                          startEdit(contact)
                        }}
                      >
                        Düzenle
                      </Button>
                    </div>
                    
                    {contact.phoneNumbers.length > 0 && (
                      <p className="text-sm text-muted-foreground">
                        {contact.phoneNumbers[0].number}
                      </p>
                    )}
                    
                    {contact.organization && (
                      <p className="text-xs text-muted-foreground">
                        {contact.organization}
                      </p>
                    )}
                  </div>
                ))}
              </div>
            )}
          </CardContent>
        </Card>

        {/* Kişi Detayları */}
        <Card>
          <CardHeader>
            <CardTitle>Kişi Detayları</CardTitle>
          </CardHeader>
          <CardContent>
            {!selectedContact ? (
              <div className="text-center py-8">
                <p className="text-muted-foreground">
                  Detayları görmek için bir kişi seçin
                </p>
              </div>
            ) : (
              <div className="space-y-4">
                <div className="flex items-center justify-between">
                  <h3 className="text-xl font-semibold">{selectedContact.name}</h3>
                  <div className="flex items-center gap-2">
                    {selectedContact.favorite && (
                      <Badge variant="secondary">★ Favori</Badge>
                    )}
                    <Button
                      size="sm"
                      variant="outline"
                      onClick={() => toggleFavorite(selectedContact.id)}
                    >
                      {selectedContact.favorite ? 'Favoriden Çıkar' : 'Favoriye Ekle'}
                    </Button>
                  </div>
                </div>

                {selectedContact.organization && (
                  <p className="text-muted-foreground">{selectedContact.organization}</p>
                )}

                {/* Telefon Numaraları */}
                {selectedContact.phoneNumbers.length > 0 && (
                  <div>
                    <h4 className="font-medium mb-2">Telefon Numaraları</h4>
                    <div className="space-y-1">
                      {selectedContact.phoneNumbers.map((phone, index) => (
                        <div key={index} className="flex items-center justify-between p-2 border rounded">
                          <div>
                            <p className="font-medium">{phone.number}</p>
                            <p className="text-sm text-muted-foreground">{phone.type}</p>
                          </div>
                          <Button size="sm" variant="outline">
                            Ara
                          </Button>
                        </div>
                      ))}
                    </div>
                  </div>
                )}

                {/* E-posta Adresleri */}
                {selectedContact.emails.length > 0 && (
                  <div>
                    <h4 className="font-medium mb-2">E-posta Adresleri</h4>
                    <div className="space-y-1">
                      {selectedContact.emails.map((email, index) => (
                        <div key={index} className="flex items-center justify-between p-2 border rounded">
                          <div>
                            <p className="font-medium">{email.email}</p>
                            <p className="text-sm text-muted-foreground">{email.type}</p>
                          </div>
                          <Button size="sm" variant="outline">
                            E-posta Gönder
                          </Button>
                        </div>
                      ))}
                    </div>
                  </div>
                )}

                {/* Notlar */}
                {selectedContact.notes && (
                  <div>
                    <h4 className="font-medium mb-2">Notlar</h4>
                    <p className="text-sm p-2 border rounded bg-muted/50">
                      {selectedContact.notes}
                    </p>
                  </div>
                )}

                {/* Son Güncelleme */}
                <div className="text-xs text-muted-foreground">
                  Son güncelleme: {selectedContact.lastModified}
                </div>

                {/* İşlemler */}
                <div className="flex gap-2">
                  <Button
                    variant="outline"
                    onClick={() => startEdit(selectedContact)}
                  >
                    Düzenle
                  </Button>
                  <Button
                    variant="outline"
                    onClick={() => deleteContact(selectedContact.id)}
                  >
                    Sil
                  </Button>
                </div>
              </div>
            )}
          </CardContent>
        </Card>
      </div>
    </div>
  )
}
