'use client'

import { useState, useRef, useEffect } from 'react'
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card'
import { Button } from '@/components/ui/button'
import { Slider } from '@/components/ui/slider'
import { Label } from '@/components/ui/label'
import { Badge } from '@/components/ui/badge'
import { Alert, AlertDescription } from '@/components/ui/alert'

interface ScreenMonitorProps {
  deviceId: string
  isConnected: boolean
}

export default function ScreenMonitor({ deviceId, isConnected }: ScreenMonitorProps) {
  const [isStreaming, setIsStreaming] = useState(false)
  const [quality, setQuality] = useState([75])
  const [fps, setFps] = useState([30])
  const [error, setError] = useState('')
  const [connectionStatus, setConnectionStatus] = useState<'idle' | 'connecting' | 'streaming' | 'error'>('idle')
  const canvasRef = useRef<HTMLCanvasElement>(null)
  const streamRef = useRef<MediaStream | null>(null)

  useEffect(() => {
    return () => {
      // Cleanup stream when component unmounts
      if (streamRef.current) {
        streamRef.current.getTracks().forEach(track => track.stop())
      }
    }
  }, [])

  const startScreenStream = async () => {
    if (!isConnected) {
      setError('Cihaz bağlı değil')
      return
    }

    setError('')
    setConnectionStatus('connecting')

    try {
      // Simüle ekran akışı başlatma
      await new Promise(resolve => setTimeout(resolve, 1500))
      
      setIsStreaming(true)
      setConnectionStatus('streaming')
      
      // Simüle canvas üzerinde ekran görüntüsü
      simulateScreenContent()
      
    } catch (err) {
      setError('Ekran akışı başlatılamadı')
      setConnectionStatus('error')
    }
  }

  const stopScreenStream = () => {
    setIsStreaming(false)
    setConnectionStatus('idle')
    
    if (streamRef.current) {
      streamRef.current.getTracks().forEach(track => track.stop())
      streamRef.current = null
    }

    // Canvas'ı temizle
    const canvas = canvasRef.current
    if (canvas) {
      const ctx = canvas.getContext('2d')
      ctx?.clearRect(0, 0, canvas.width, canvas.height)
    }
  }

  const simulateScreenContent = () => {
    const canvas = canvasRef.current
    if (!canvas) return

    const ctx = canvas.getContext('2d')
    if (!ctx) return

    // Simüle Android ekran içeriği
    ctx.fillStyle = '#1a1a1a'
    ctx.fillRect(0, 0, canvas.width, canvas.height)
    
    // Simüle status bar
    ctx.fillStyle = '#333'
    ctx.fillRect(0, 0, canvas.width, 60)
    
    // Simüle metin
    ctx.fillStyle = '#fff'
    ctx.font = '16px Arial'
    ctx.fillText('Android Ekran Simülasyonu', 20, 100)
    ctx.fillText('Kalite: ' + quality[0] + '%', 20, 130)
    ctx.fillText('FPS: ' + fps[0], 20, 160)
    
    // Simüle app icons
    for (let i = 0; i < 12; i++) {
      const x = 50 + (i % 4) * 80
      const y = 200 + Math.floor(i / 4) * 80
      ctx.fillStyle = `hsl(${i * 30}, 70%, 50%)`
      ctx.fillRect(x, y, 60, 60)
    }
  }

  const takeScreenshot = async () => {
    if (!isStreaming) {
      setError('Ekran akışı aktif değil')
      return
    }

    try {
      const canvas = canvasRef.current
      if (!canvas) return

      // Canvas'tan screenshot al
      const dataURL = canvas.toDataURL('image/png')
      
      // Download link oluştur
      const link = document.createElement('a')
      link.download = `screenshot-${new Date().getTime()}.png`
      link.href = dataURL
      link.click()
      
    } catch (err) {
      setError('Ekran görüntüsü alınamadı')
    }
  }

  const handleCanvasClick = (event: React.MouseEvent<HTMLCanvasElement>) => {
    if (!isStreaming) return

    const canvas = canvasRef.current
    if (!canvas) return

    const rect = canvas.getBoundingClientRect()
    const x = event.clientX - rect.left
    const y = event.clientY - rect.top

    // Koordinatları cihaza gönder (simüle)
    console.log(`Touch at: ${x}, ${y}`)
    
    // Görsel feedback
    const ctx = canvas.getContext('2d')
    if (ctx) {
      ctx.fillStyle = 'rgba(255, 255, 255, 0.5)'
      ctx.beginPath()
      ctx.arc(x, y, 20, 0, 2 * Math.PI)
      ctx.fill()
      
      // Feedback'i kısa süre sonra temizle
      setTimeout(() => {
        simulateScreenContent()
      }, 200)
    }
  }

  const getStatusColor = () => {
    switch (connectionStatus) {
      case 'streaming': return 'bg-green-500'
      case 'connecting': return 'bg-yellow-500'
      case 'error': return 'bg-red-500'
      default: return 'bg-gray-500'
    }
  }

  const getStatusText = () => {
    switch (connectionStatus) {
      case 'streaming': return 'Akış Aktif'
      case 'connecting': return 'Bağlanıyor'
      case 'error': return 'Hata'
      default: return 'Beklemede'
    }
  }

  return (
    <div className="space-y-6">
      <Card>
        <CardHeader>
          <div className="flex items-center justify-between">
            <div>
              <CardTitle>Ekran İzleme</CardTitle>
              <CardDescription>
                Cihazınızın ekranını canlı olarak izleyin ve kontrol edin
              </CardDescription>
            </div>
            <Badge className={getStatusColor()}>
              {getStatusText()}
            </Badge>
          </div>
        </CardHeader>
        <CardContent className="space-y-4">
          {/* Kontrol Butonları */}
          <div className="flex gap-2">
            {!isStreaming ? (
              <Button 
                onClick={startScreenStream}
                disabled={!isConnected || connectionStatus === 'connecting'}
              >
                {connectionStatus === 'connecting' ? 'Başlatılıyor...' : 'Ekran Akışını Başlat'}
              </Button>
            ) : (
              <Button onClick={stopScreenStream} variant="destructive">
                Akışı Durdur
              </Button>
            )}
            
            <Button 
              onClick={takeScreenshot}
              disabled={!isStreaming}
              variant="outline"
            >
              Ekran Görüntüsü Al
            </Button>
          </div>

          {/* Kalite Ayarları */}
          <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
            <div className="space-y-2">
              <Label>Kalite: {quality[0]}%</Label>
              <Slider
                value={quality}
                onValueChange={setQuality}
                max={100}
                min={25}
                step={25}
                disabled={isStreaming}
              />
            </div>
            
            <div className="space-y-2">
              <Label>FPS: {fps[0]}</Label>
              <Slider
                value={fps}
                onValueChange={setFps}
                max={60}
                min={15}
                step={15}
                disabled={isStreaming}
              />
            </div>
          </div>

          {error && (
            <Alert>
              <AlertDescription>{error}</AlertDescription>
            </Alert>
          )}
        </CardContent>
      </Card>

      {/* Ekran Görüntüsü */}
      <Card>
        <CardContent className="p-4">
          <div className="relative">
            <canvas
              ref={canvasRef}
              width={360}
              height={640}
              className="w-full max-w-md mx-auto border rounded-lg cursor-pointer bg-black"
              onClick={handleCanvasClick}
              style={{ aspectRatio: '9/16' }}
            />
            {!isStreaming && (
              <div className="absolute inset-0 flex items-center justify-center bg-gray-100 rounded-lg">
                <div className="text-center">
                  <p className="text-lg font-medium text-gray-600 mb-2">
                    Ekran Akışı Kapalı
                  </p>
                  <p className="text-sm text-gray-500">
                    Cihaz ekranını görüntülemek için akışı başlatın
                  </p>
                </div>
              </div>
            )}
          </div>
          
          {isStreaming && (
            <div className="mt-4 text-center">
              <p className="text-sm text-muted-foreground">
                Ekrana dokunarak cihazı kontrol edebilirsiniz
              </p>
            </div>
          )}
        </CardContent>
      </Card>
    </div>
  )
}
