'use client'

import { useState, useEffect } from 'react'
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card'
import { Button } from '@/components/ui/button'
import { Badge } from '@/components/ui/badge'
import { Alert, AlertDescription } from '@/components/ui/alert'
import { Tabs, TabsContent, TabsList, TabsTrigger } from '@/components/ui/tabs'

interface LocationData {
  id: string
  latitude: number
  longitude: number
  accuracy: number
  timestamp: string
  address?: string
}

interface LocationMapProps {
  deviceId: string
  isConnected: boolean
}

export default function LocationMap({ deviceId, isConnected }: LocationMapProps) {
  const [isTracking, setIsTracking] = useState(false)
  const [currentLocation, setCurrentLocation] = useState<LocationData | null>(null)
  const [locationHistory, setLocationHistory] = useState<LocationData[]>([])
  const [error, setError] = useState('')
  const [trackingStatus, setTrackingStatus] = useState<'idle' | 'starting' | 'active' | 'error'>('idle')

  // Simüle konum verileri (İstanbul merkezi)
  const simulateLocationData = (): LocationData => {
    const baseLatitude = 41.0082
    const baseLongitude = 28.9784
    
    // Rastgele küçük sapma ekle
    const latVariation = (Math.random() - 0.5) * 0.01
    const lngVariation = (Math.random() - 0.5) * 0.01
    
    return {
      id: Date.now().toString(),
      latitude: baseLatitude + latVariation,
      longitude: baseLongitude + lngVariation,
      accuracy: Math.floor(Math.random() * 20) + 5,
      timestamp: new Date().toLocaleString('tr-TR'),
      address: 'İstanbul, Türkiye'
    }
  }

  const startLocationTracking = async () => {
    if (!isConnected) {
      setError('Cihaz bağlı değil')
      return
    }

    setError('')
    setTrackingStatus('starting')

    try {
      // Simüle konum izni ve başlatma
      await new Promise(resolve => setTimeout(resolve, 1500))
      
      setIsTracking(true)
      setTrackingStatus('active')
      
      // İlk konum verisini al
      const initialLocation = simulateLocationData()
      setCurrentLocation(initialLocation)
      setLocationHistory(prev => [initialLocation, ...prev])
      
    } catch (err) {
      setError('Konum takibi başlatılamadı')
      setTrackingStatus('error')
    }
  }

  const stopLocationTracking = () => {
    setIsTracking(false)
    setTrackingStatus('idle')
  }

  const refreshLocation = async () => {
    if (!isTracking) {
      setError('Konum takibi aktif değil')
      return
    }

    try {
      const newLocation = simulateLocationData()
      setCurrentLocation(newLocation)
      setLocationHistory(prev => [newLocation, ...prev.slice(0, 49)]) // Son 50 konum
    } catch (err) {
      setError('Konum güncellenemedi')
    }
  }

  const clearLocationHistory = () => {
    setLocationHistory([])
  }

  // Otomatik konum güncelleme
  useEffect(() => {
    if (!isTracking) return

    const interval = setInterval(() => {
      refreshLocation()
    }, 10000) // Her 10 saniyede bir güncelle

    return () => clearInterval(interval)
  }, [isTracking])

  const getStatusColor = () => {
    switch (trackingStatus) {
      case 'active': return 'bg-green-500'
      case 'starting': return 'bg-yellow-500'
      case 'error': return 'bg-red-500'
      default: return 'bg-gray-500'
    }
  }

  const getStatusText = () => {
    switch (trackingStatus) {
      case 'active': return 'Takip Aktif'
      case 'starting': return 'Başlatılıyor'
      case 'error': return 'Hata'
      default: return 'Beklemede'
    }
  }

  const formatCoordinate = (coord: number, type: 'lat' | 'lng') => {
    const direction = type === 'lat' ? (coord >= 0 ? 'K' : 'G') : (coord >= 0 ? 'D' : 'B')
    return `${Math.abs(coord).toFixed(6)}° ${direction}`
  }

  const openInMaps = (location: LocationData) => {
    const url = `https://www.openstreetmap.org/?mlat=${location.latitude}&mlon=${location.longitude}&zoom=15`
    window.open(url, '_blank')
  }

  return (
    <div className="space-y-6">
      <Card>
        <CardHeader>
          <div className="flex items-center justify-between">
            <div>
              <CardTitle>Konum Takibi</CardTitle>
              <CardDescription>
                Cihazınızın konumunu gerçek zamanlı olarak takip edin
              </CardDescription>
            </div>
            <Badge className={getStatusColor()}>
              {getStatusText()}
            </Badge>
          </div>
        </CardHeader>
        <CardContent className="space-y-4">
          {/* Kontrol Butonları */}
          <div className="flex flex-wrap gap-2">
            {!isTracking ? (
              <Button 
                onClick={startLocationTracking}
                disabled={!isConnected || trackingStatus === 'starting'}
              >
                {trackingStatus === 'starting' ? 'Başlatılıyor...' : 'Konum Takibini Başlat'}
              </Button>
            ) : (
              <>
                <Button onClick={stopLocationTracking} variant="destructive">
                  Takibi Durdur
                </Button>
                <Button onClick={refreshLocation} variant="outline">
                  Konumu Yenile
                </Button>
                <Button onClick={clearLocationHistory} variant="outline">
                  Geçmişi Temizle
                </Button>
              </>
            )}
          </div>

          {error && (
            <Alert>
              <AlertDescription>{error}</AlertDescription>
            </Alert>
          )}

          {/* Mevcut Konum Bilgisi */}
          {currentLocation && (
            <div className="p-4 border rounded-lg bg-muted/50">
              <h4 className="font-medium mb-2">Güncel Konum</h4>
              <div className="grid grid-cols-1 md:grid-cols-2 gap-4 text-sm">
                <div>
                  <p><strong>Enlem:</strong> {formatCoordinate(currentLocation.latitude, 'lat')}</p>
                  <p><strong>Boylam:</strong> {formatCoordinate(currentLocation.longitude, 'lng')}</p>
                </div>
                <div>
                  <p><strong>Doğruluk:</strong> ±{currentLocation.accuracy}m</p>
                  <p><strong>Zaman:</strong> {currentLocation.timestamp}</p>
                </div>
              </div>
              {currentLocation.address && (
                <p className="mt-2 text-sm"><strong>Adres:</strong> {currentLocation.address}</p>
              )}
              <Button 
                onClick={() => openInMaps(currentLocation)}
                size="sm"
                variant="outline"
                className="mt-2"
              >
                Haritada Göster
              </Button>
            </div>
          )}
        </CardContent>
      </Card>

      {/* Harita ve Geçmiş */}
      <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
        {/* Simüle Harita */}
        <Card>
          <CardHeader>
            <CardTitle>Harita Görünümü</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="relative">
              <div className="w-full h-64 bg-gradient-to-br from-green-100 to-blue-100 rounded-lg border flex items-center justify-center">
                {currentLocation ? (
                  <div className="text-center">
                    <div className="w-4 h-4 bg-red-500 rounded-full mx-auto mb-2 animate-pulse"></div>
                    <p className="text-sm font-medium">Güncel Konum</p>
                    <p className="text-xs text-muted-foreground">
                      {formatCoordinate(currentLocation.latitude, 'lat')}, {formatCoordinate(currentLocation.longitude, 'lng')}
                    </p>
                  </div>
                ) : (
                  <div className="text-center">
                    <p className="text-lg font-medium text-gray-600 mb-2">
                      Harita Görünümü
                    </p>
                    <p className="text-sm text-gray-500">
                      Konum takibini başlatın
                    </p>
                  </div>
                )}
              </div>
              
              {/* Harita Kontrolleri */}
              <div className="absolute top-2 right-2 flex flex-col gap-1">
                <Button size="sm" variant="outline" className="w-8 h-8 p-0">+</Button>
                <Button size="sm" variant="outline" className="w-8 h-8 p-0">-</Button>
              </div>
            </div>
            
            <div className="mt-4 text-center">
              <p className="text-xs text-muted-foreground">
                Gerçek uygulamada OpenStreetMap entegrasyonu kullanılacak
              </p>
            </div>
          </CardContent>
        </Card>

        {/* Konum Geçmişi */}
        <Card>
          <CardHeader>
            <CardTitle>Konum Geçmişi ({locationHistory.length})</CardTitle>
          </CardHeader>
          <CardContent>
            {locationHistory.length === 0 ? (
              <div className="text-center py-8">
                <p className="text-muted-foreground">
                  Henüz konum kaydı yok
                </p>
              </div>
            ) : (
              <div className="space-y-2 max-h-80 overflow-y-auto">
                {locationHistory.map((location, index) => (
                  <div key={location.id} className="p-3 border rounded-lg">
                    <div className="flex items-center justify-between mb-2">
                      <Badge variant={index === 0 ? 'default' : 'secondary'}>
                        {index === 0 ? 'Güncel' : `${index + 1}. Kayıt`}
                      </Badge>
                      <span className="text-xs text-muted-foreground">
                        {location.timestamp}
                      </span>
                    </div>
                    
                    <div className="text-sm space-y-1">
                      <p>
                        <strong>Konum:</strong> {formatCoordinate(location.latitude, 'lat')}, {formatCoordinate(location.longitude, 'lng')}
                      </p>
                      <p>
                        <strong>Doğruluk:</strong> ±{location.accuracy}m
                      </p>
                      {location.address && (
                        <p>
                          <strong>Adres:</strong> {location.address}
                        </p>
                      )}
                    </div>
                    
                    <Button
                      size="sm"
                      variant="outline"
                      onClick={() => openInMaps(location)}
                      className="mt-2"
                    >
                      Haritada Göster
                    </Button>
                  </div>
                ))}
              </div>
            )}
          </CardContent>
        </Card>
      </div>
    </div>
  )
}
