'use client'

import { useState, useEffect } from 'react'
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card'
import { Button } from '@/components/ui/button'
import { Input } from '@/components/ui/input'
import { Textarea } from '@/components/ui/textarea'
import { Label } from '@/components/ui/label'
import { Badge } from '@/components/ui/badge'
import { Alert, AlertDescription } from '@/components/ui/alert'
import { Tabs, TabsContent, TabsList, TabsTrigger } from '@/components/ui/tabs'
import { Dialog, DialogContent, DialogDescription, DialogHeader, DialogTitle, DialogTrigger } from '@/components/ui/dialog'

interface SMSMessage {
  id: string
  phoneNumber: string
  contactName?: string
  message: string
  timestamp: string
  type: 'incoming' | 'outgoing'
  status: 'sent' | 'delivered' | 'failed' | 'pending'
}

interface SMSManagerProps {
  deviceId: string
  isConnected: boolean
}

export default function SMSManager({ deviceId, isConnected }: SMSManagerProps) {
  const [messages, setMessages] = useState<SMSMessage[]>([])
  const [isLoading, setIsLoading] = useState(false)
  const [error, setError] = useState('')
  const [newMessage, setNewMessage] = useState({ phoneNumber: '', message: '' })
  const [isSending, setIsSending] = useState(false)
  const [selectedMessage, setSelectedMessage] = useState<SMSMessage | null>(null)
  const [searchTerm, setSearchTerm] = useState('')

  // Simüle SMS verileri
  const generateSampleMessages = (): SMSMessage[] => {
    const sampleMessages: SMSMessage[] = [
      {
        id: '1',
        phoneNumber: '+905551234567',
        contactName: 'Ahmet Yılmaz',
        message: 'Merhaba, toplantı saat kaçta?',
        timestamp: new Date(Date.now() - 3600000).toLocaleString('tr-TR'),
        type: 'incoming',
        status: 'delivered'
      },
      {
        id: '2',
        phoneNumber: '+905551234567',
        contactName: 'Ahmet Yılmaz',
        message: 'Toplantı saat 14:00\'da başlayacak.',
        timestamp: new Date(Date.now() - 3000000).toLocaleString('tr-TR'),
        type: 'outgoing',
        status: 'delivered'
      },
      {
        id: '3',
        phoneNumber: '+905559876543',
        contactName: 'Fatma Demir',
        message: 'Bugün akşam yemeğe gelebilir misin?',
        timestamp: new Date(Date.now() - 7200000).toLocaleString('tr-TR'),
        type: 'incoming',
        status: 'delivered'
      },
      {
        id: '4',
        phoneNumber: '+905551111111',
        message: 'Doğrulama kodunuz: 123456',
        timestamp: new Date(Date.now() - 86400000).toLocaleString('tr-TR'),
        type: 'incoming',
        status: 'delivered'
      }
    ]
    return sampleMessages
  }

  const loadMessages = async () => {
    if (!isConnected) {
      setError('Cihaz bağlı değil')
      return
    }

    setIsLoading(true)
    setError('')

    try {
      // Simüle SMS yükleme
      await new Promise(resolve => setTimeout(resolve, 1500))
      
      const sampleMessages = generateSampleMessages()
      setMessages(sampleMessages)
      
    } catch (err) {
      setError('SMS mesajları yüklenemedi')
    } finally {
      setIsLoading(false)
    }
  }

  const sendMessage = async () => {
    if (!newMessage.phoneNumber.trim() || !newMessage.message.trim()) {
      setError('Telefon numarası ve mesaj gereklidir')
      return
    }

    if (!isConnected) {
      setError('Cihaz bağlı değil')
      return
    }

    setIsSending(true)
    setError('')

    try {
      // Simüle SMS gönderme
      await new Promise(resolve => setTimeout(resolve, 2000))
      
      const sentMessage: SMSMessage = {
        id: Date.now().toString(),
        phoneNumber: newMessage.phoneNumber,
        message: newMessage.message,
        timestamp: new Date().toLocaleString('tr-TR'),
        type: 'outgoing',
        status: 'sent'
      }
      
      setMessages(prev => [sentMessage, ...prev])
      setNewMessage({ phoneNumber: '', message: '' })
      
      // Durum güncellemesi simülasyonu
      setTimeout(() => {
        setMessages(prev => 
          prev.map(msg => 
            msg.id === sentMessage.id 
              ? { ...msg, status: 'delivered' }
              : msg
          )
        )
      }, 3000)
      
    } catch (err) {
      setError('Mesaj gönderilemedi')
    } finally {
      setIsSending(false)
    }
  }

  const deleteMessage = async (messageId: string) => {
    try {
      // Simüle SMS silme
      await new Promise(resolve => setTimeout(resolve, 500))
      
      setMessages(prev => prev.filter(msg => msg.id !== messageId))
      setSelectedMessage(null)
      
    } catch (err) {
      setError('Mesaj silinemedi')
    }
  }

  const getStatusColor = (status: string) => {
    switch (status) {
      case 'delivered': return 'bg-green-500'
      case 'sent': return 'bg-blue-500'
      case 'pending': return 'bg-yellow-500'
      case 'failed': return 'bg-red-500'
      default: return 'bg-gray-500'
    }
  }

  const getStatusText = (status: string) => {
    switch (status) {
      case 'delivered': return 'Teslim Edildi'
      case 'sent': return 'Gönderildi'
      case 'pending': return 'Beklemede'
      case 'failed': return 'Başarısız'
      default: return 'Bilinmiyor'
    }
  }

  const filteredMessages = messages.filter(msg => 
    msg.message.toLowerCase().includes(searchTerm.toLowerCase()) ||
    msg.phoneNumber.includes(searchTerm) ||
    (msg.contactName && msg.contactName.toLowerCase().includes(searchTerm.toLowerCase()))
  )

  const groupMessagesByContact = () => {
    const grouped: { [key: string]: SMSMessage[] } = {}
    
    filteredMessages.forEach(msg => {
      const key = msg.contactName || msg.phoneNumber
      if (!grouped[key]) {
        grouped[key] = []
      }
      grouped[key].push(msg)
    })
    
    return grouped
  }

  useEffect(() => {
    if (isConnected && messages.length === 0) {
      loadMessages()
    }
  }, [isConnected])

  return (
    <div className="space-y-6">
      <Card>
        <CardHeader>
          <CardTitle>SMS Yönetimi</CardTitle>
          <CardDescription>
            SMS mesajlarını görüntüleyin, gönderin ve yönetin
          </CardDescription>
        </CardHeader>
        <CardContent className="space-y-4">
          {/* Kontrol Butonları */}
          <div className="flex flex-wrap gap-2">
            <Button 
              onClick={loadMessages}
              disabled={!isConnected || isLoading}
            >
              {isLoading ? 'Yükleniyor...' : 'Mesajları Yenile'}
            </Button>
            
            <Dialog>
              <DialogTrigger asChild>
                <Button variant="outline" disabled={!isConnected}>
                  Yeni Mesaj
                </Button>
              </DialogTrigger>
              <DialogContent>
                <DialogHeader>
                  <DialogTitle>Yeni SMS Gönder</DialogTitle>
                  <DialogDescription>
                    Yeni bir SMS mesajı gönderin
                  </DialogDescription>
                </DialogHeader>
                <div className="space-y-4">
                  <div className="space-y-2">
                    <Label htmlFor="phone">Telefon Numarası</Label>
                    <Input
                      id="phone"
                      placeholder="+905551234567"
                      value={newMessage.phoneNumber}
                      onChange={(e) => setNewMessage(prev => ({ ...prev, phoneNumber: e.target.value }))}
                    />
                  </div>
                  
                  <div className="space-y-2">
                    <Label htmlFor="message">Mesaj</Label>
                    <Textarea
                      id="message"
                      placeholder="Mesajınızı yazın..."
                      value={newMessage.message}
                      onChange={(e) => setNewMessage(prev => ({ ...prev, message: e.target.value }))}
                      rows={4}
                    />
                    <p className="text-xs text-muted-foreground">
                      {newMessage.message.length}/160 karakter
                    </p>
                  </div>
                  
                  <Button 
                    onClick={sendMessage}
                    disabled={isSending || !newMessage.phoneNumber.trim() || !newMessage.message.trim()}
                    className="w-full"
                  >
                    {isSending ? 'Gönderiliyor...' : 'Mesaj Gönder'}
                  </Button>
                </div>
              </DialogContent>
            </Dialog>
          </div>

          {error && (
            <Alert>
              <AlertDescription>{error}</AlertDescription>
            </Alert>
          )}

          {/* Arama */}
          <div className="space-y-2">
            <Label htmlFor="search">Mesajlarda Ara</Label>
            <Input
              id="search"
              placeholder="Kişi adı, numara veya mesaj içeriği..."
              value={searchTerm}
              onChange={(e) => setSearchTerm(e.target.value)}
            />
          </div>
        </CardContent>
      </Card>

      {/* Mesaj Listesi */}
      <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
        {/* Konuşmalar */}
        <Card>
          <CardHeader>
            <CardTitle>Konuşmalar ({Object.keys(groupMessagesByContact()).length})</CardTitle>
          </CardHeader>
          <CardContent>
            {Object.keys(groupMessagesByContact()).length === 0 ? (
              <div className="text-center py-8">
                <p className="text-muted-foreground">
                  {messages.length === 0 ? 'Henüz mesaj yok' : 'Arama sonucu bulunamadı'}
                </p>
              </div>
            ) : (
              <div className="space-y-2 max-h-96 overflow-y-auto">
                {Object.entries(groupMessagesByContact()).map(([contact, contactMessages]) => {
                  const lastMessage = contactMessages[0]
                  const unreadCount = contactMessages.filter(msg => msg.type === 'incoming').length
                  
                  return (
                    <div 
                      key={contact}
                      className="p-3 border rounded-lg cursor-pointer hover:bg-muted/50"
                      onClick={() => setSelectedMessage(lastMessage)}
                    >
                      <div className="flex items-center justify-between mb-2">
                        <h4 className="font-medium">{contact}</h4>
                        <div className="flex items-center gap-2">
                          {unreadCount > 0 && (
                            <Badge variant="secondary">{unreadCount}</Badge>
                          )}
                          <span className="text-xs text-muted-foreground">
                            {lastMessage.timestamp}
                          </span>
                        </div>
                      </div>
                      
                      <p className="text-sm text-muted-foreground truncate">
                        {lastMessage.type === 'outgoing' ? 'Sen: ' : ''}
                        {lastMessage.message}
                      </p>
                      
                      <div className="flex items-center justify-between mt-2">
                        <Badge variant="outline">
                          {contactMessages.length} mesaj
                        </Badge>
                        {lastMessage.type === 'outgoing' && (
                          <Badge className={getStatusColor(lastMessage.status)}>
                            {getStatusText(lastMessage.status)}
                          </Badge>
                        )}
                      </div>
                    </div>
                  )
                })}
              </div>
            )}
          </CardContent>
        </Card>

        {/* Mesaj Detayları */}
        <Card>
          <CardHeader>
            <CardTitle>Mesaj Detayları</CardTitle>
          </CardHeader>
          <CardContent>
            {!selectedMessage ? (
              <div className="text-center py-8">
                <p className="text-muted-foreground">
                  Detayları görmek için bir konuşma seçin
                </p>
              </div>
            ) : (
              <div className="space-y-4">
                <div className="p-4 border rounded-lg">
                  <div className="flex items-center justify-between mb-3">
                    <div>
                      <h4 className="font-medium">
                        {selectedMessage.contactName || selectedMessage.phoneNumber}
                      </h4>
                      <p className="text-sm text-muted-foreground">
                        {selectedMessage.phoneNumber}
                      </p>
                    </div>
                    <Badge variant={selectedMessage.type === 'incoming' ? 'secondary' : 'default'}>
                      {selectedMessage.type === 'incoming' ? 'Gelen' : 'Giden'}
                    </Badge>
                  </div>
                  
                  <div className="space-y-2">
                    <p className="text-sm">{selectedMessage.message}</p>
                    <div className="flex items-center justify-between text-xs text-muted-foreground">
                      <span>{selectedMessage.timestamp}</span>
                      {selectedMessage.type === 'outgoing' && (
                        <Badge className={getStatusColor(selectedMessage.status)}>
                          {getStatusText(selectedMessage.status)}
                        </Badge>
                      )}
                    </div>
                  </div>
                </div>
                
                {/* Mesaj İşlemleri */}
                <div className="flex gap-2">
                  <Button
                    variant="outline"
                    onClick={() => setNewMessage(prev => ({ 
                      ...prev, 
                      phoneNumber: selectedMessage.phoneNumber 
                    }))}
                  >
                    Yanıtla
                  </Button>
                  <Button
                    variant="outline"
                    onClick={() => deleteMessage(selectedMessage.id)}
                  >
                    Sil
                  </Button>
                </div>
                
                {/* Aynı kişiden diğer mesajlar */}
                <div className="space-y-2">
                  <h5 className="font-medium text-sm">Bu kişiden diğer mesajlar:</h5>
                  <div className="max-h-40 overflow-y-auto space-y-1">
                    {messages
                      .filter(msg => 
                        msg.phoneNumber === selectedMessage.phoneNumber && 
                        msg.id !== selectedMessage.id
                      )
                      .map(msg => (
                        <div key={msg.id} className="p-2 border rounded text-sm">
                          <div className="flex items-center justify-between mb-1">
                            <Badge variant={msg.type === 'incoming' ? 'secondary' : 'default'} className="text-xs">
                              {msg.type === 'incoming' ? 'Gelen' : 'Giden'}
                            </Badge>
                            <span className="text-xs text-muted-foreground">
                              {msg.timestamp}
                            </span>
                          </div>
                          <p className="text-xs">{msg.message}</p>
                        </div>
                      ))}
                  </div>
                </div>
              </div>
            )}
          </CardContent>
        </Card>
      </div>
    </div>
  )
}
