'use client'

import { useState } from 'react'
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card'
import { Button } from '@/components/ui/button'
import { Input } from '@/components/ui/input'
import { Label } from '@/components/ui/label'
import { Badge } from '@/components/ui/badge'
import { Alert, AlertDescription } from '@/components/ui/alert'
import { Dialog, DialogContent, DialogDescription, DialogHeader, DialogTitle, DialogTrigger } from '@/components/ui/dialog'
import { Checkbox } from '@/components/ui/checkbox'
import { Progress } from '@/components/ui/progress'

interface DeviceWipeProps {
  deviceId: string
  isConnected: boolean
}

interface WipeOption {
  id: string
  title: string
  description: string
  dangerous: boolean
  enabled: boolean
}

export default function DeviceWipe({ deviceId, isConnected }: DeviceWipeProps) {
  const [isWipeDialogOpen, setIsWipeDialogOpen] = useState(false)
  const [confirmationText, setConfirmationText] = useState('')
  const [selectedWipeOptions, setSelectedWipeOptions] = useState<string[]>([])
  const [isWiping, setIsWiping] = useState(false)
  const [wipeProgress, setWipeProgress] = useState(0)
  const [error, setError] = useState('')
  const [lastWipeDate, setLastWipeDate] = useState<string | null>(null)

  const wipeOptions: WipeOption[] = [
    {
      id: 'cache',
      title: 'Önbellek Temizleme',
      description: 'Uygulama önbelleklerini ve geçici dosyaları temizler',
      dangerous: false,
      enabled: true
    },
    {
      id: 'downloads',
      title: 'İndirilenler Klasörü',
      description: 'İndirilenler klasöründeki tüm dosyaları siler',
      dangerous: false,
      enabled: true
    },
    {
      id: 'photos',
      title: 'Fotoğraf ve Videolar',
      description: 'Galeri, kamera ve medya dosyalarını siler',
      dangerous: true,
      enabled: true
    },
    {
      id: 'messages',
      title: 'Mesajlar ve Konuşmalar',
      description: 'SMS, WhatsApp ve diğer mesajlaşma uygulamalarını siler',
      dangerous: true,
      enabled: true
    },
    {
      id: 'contacts',
      title: 'Kişiler ve Rehber',
      description: 'Telefon rehberindeki tüm kişileri siler',
      dangerous: true,
      enabled: true
    },
    {
      id: 'apps',
      title: 'Kullanıcı Uygulamaları',
      description: 'Yüklenen tüm uygulamaları ve verilerini siler',
      dangerous: true,
      enabled: true
    },
    {
      id: 'accounts',
      title: 'Hesaplar ve Oturumlar',
      description: 'Google, sosyal medya ve diğer hesap oturumlarını kapatır',
      dangerous: true,
      enabled: true
    },
    {
      id: 'factory',
      title: 'Fabrika Ayarlarına Sıfırlama',
      description: 'Cihazı tamamen fabrika ayarlarına döndürür (GERİ ALINAMAZ)',
      dangerous: true,
      enabled: true
    }
  ]

  const handleWipeOptionToggle = (optionId: string) => {
    setSelectedWipeOptions(prev => 
      prev.includes(optionId)
        ? prev.filter(id => id !== optionId)
        : [...prev, optionId]
    )
  }

  const getSelectedOptionsInfo = () => {
    const selected = wipeOptions.filter(option => selectedWipeOptions.includes(option.id))
    const dangerousCount = selected.filter(option => option.dangerous).length
    const safeCount = selected.filter(option => !option.dangerous).length
    
    return { selected, dangerousCount, safeCount, total: selected.length }
  }

  const startWipeProcess = async () => {
    if (!isConnected) {
      setError('Cihaz bağlı değil')
      return
    }

    if (selectedWipeOptions.length === 0) {
      setError('En az bir temizleme seçeneği seçin')
      return
    }

    if (confirmationText !== 'SİL') {
      setError('Onay için "SİL" yazın')
      return
    }

    setIsWiping(true)
    setWipeProgress(0)
    setError('')

    try {
      const selectedOptions = wipeOptions.filter(option => selectedWipeOptions.includes(option.id))
      
      for (let i = 0; i < selectedOptions.length; i++) {
        const option = selectedOptions[i]
        
        // Her seçenek için simüle işlem
        await new Promise(resolve => {
          const duration = option.dangerous ? 3000 : 1500 // Tehlikeli işlemler daha uzun sürer
          const startTime = Date.now()
          
          const updateProgress = () => {
            const elapsed = Date.now() - startTime
            const optionProgress = Math.min(elapsed / duration, 1)
            const totalProgress = ((i + optionProgress) / selectedOptions.length) * 100
            
            setWipeProgress(Math.round(totalProgress))
            
            if (optionProgress < 1) {
              requestAnimationFrame(updateProgress)
            } else {
              resolve(undefined)
            }
          }
          
          updateProgress()
        })
      }
      
      setLastWipeDate(new Date().toLocaleString('tr-TR'))
      setIsWipeDialogOpen(false)
      setConfirmationText('')
      setSelectedWipeOptions([])
      
    } catch (err) {
      setError('Temizleme işlemi başarısız oldu')
    } finally {
      setIsWiping(false)
      setWipeProgress(0)
    }
  }

  const cancelWipeProcess = () => {
    setIsWipeDialogOpen(false)
    setConfirmationText('')
    setSelectedWipeOptions([])
    setError('')
  }

  const { selected, dangerousCount, safeCount, total } = getSelectedOptionsInfo()

  return (
    <div className="space-y-6">
      <Card>
        <CardHeader>
          <CardTitle className="flex items-center gap-2">
            Cihaz Temizleme ve Sıfırlama
            <Badge variant="destructive">Dikkatli Kullanın</Badge>
          </CardTitle>
          <CardDescription>
            Cihazınızı temizleyin veya fabrika ayarlarına sıfırlayın. Bu işlemler geri alınamaz!
          </CardDescription>
        </CardHeader>
        <CardContent className="space-y-4">
          {/* Uyarı */}
          <Alert>
            <AlertDescription>
              <strong>Önemli Uyarı:</strong> Bu işlemler cihazınızdaki verileri kalıcı olarak silecektir. 
              İşlem öncesinde önemli verilerinizi yedeklediğinizden emin olun.
            </AlertDescription>
          </Alert>

          {/* Son Temizleme Bilgisi */}
          {lastWipeDate && (
            <div className="p-3 border rounded-lg bg-muted/50">
              <p className="text-sm">
                <strong>Son Temizleme:</strong> {lastWipeDate}
              </p>
            </div>
          )}

          {/* Ana Temizleme Butonu */}
          <Dialog open={isWipeDialogOpen} onOpenChange={setIsWipeDialogOpen}>
            <DialogTrigger asChild>
              <Button 
                variant="destructive" 
                disabled={!isConnected}
                className="w-full"
              >
                Cihaz Temizleme Başlat
              </Button>
            </DialogTrigger>
            <DialogContent className="max-w-2xl max-h-[80vh] overflow-y-auto">
              <DialogHeader>
                <DialogTitle>Cihaz Temizleme ve Sıfırlama</DialogTitle>
                <DialogDescription>
                  Temizlemek istediğiniz alanları seçin. Kırmızı işaretli seçenekler tehlikelidir ve geri alınamaz.
                </DialogDescription>
              </DialogHeader>

              {!isWiping ? (
                <div className="space-y-6">
                  {/* Temizleme Seçenekleri */}
                  <div className="space-y-3">
                    <h4 className="font-medium">Temizleme Seçenekleri</h4>
                    {wipeOptions.map((option) => (
                      <div key={option.id} className="flex items-start space-x-3 p-3 border rounded-lg">
                        <Checkbox
                          id={option.id}
                          checked={selectedWipeOptions.includes(option.id)}
                          onCheckedChange={() => handleWipeOptionToggle(option.id)}
                          disabled={!option.enabled}
                        />
                        <div className="flex-1">
                          <div className="flex items-center gap-2">
                            <Label htmlFor={option.id} className="font-medium">
                              {option.title}
                            </Label>
                            {option.dangerous && (
                              <Badge variant="destructive" className="text-xs">
                                Tehlikeli
                              </Badge>
                            )}
                          </div>
                          <p className="text-sm text-muted-foreground mt-1">
                            {option.description}
                          </p>
                        </div>
                      </div>
                    ))}
                  </div>

                  {/* Seçim Özeti */}
                  {total > 0 && (
                    <div className="p-4 border rounded-lg bg-muted/50">
                      <h4 className="font-medium mb-2">Seçim Özeti</h4>
                      <div className="text-sm space-y-1">
                        <p><strong>Toplam Seçilen:</strong> {total} işlem</p>
                        <p><strong>Güvenli İşlemler:</strong> {safeCount}</p>
                        <p><strong>Tehlikeli İşlemler:</strong> {dangerousCount}</p>
                      </div>
                      
                      {dangerousCount > 0 && (
                        <Alert className="mt-3">
                          <AlertDescription>
                            {dangerousCount} tehlikeli işlem seçtiniz. Bu işlemler geri alınamaz!
                          </AlertDescription>
                        </Alert>
                      )}
                    </div>
                  )}

                  {/* Onay Alanı */}
                  {total > 0 && (
                    <div className="space-y-3">
                      <div className="space-y-2">
                        <Label htmlFor="confirmation">
                          Onay için "SİL" yazın (büyük harflerle)
                        </Label>
                        <Input
                          id="confirmation"
                          value={confirmationText}
                          onChange={(e) => setConfirmationText(e.target.value)}
                          placeholder="SİL"
                          className="text-center font-mono"
                        />
                      </div>

                      {error && (
                        <Alert>
                          <AlertDescription>{error}</AlertDescription>
                        </Alert>
                      )}

                      <div className="flex gap-2">
                        <Button
                          onClick={startWipeProcess}
                          disabled={confirmationText !== 'SİL' || total === 0}
                          variant="destructive"
                          className="flex-1"
                        >
                          Temizlemeyi Başlat
                        </Button>
                        <Button
                          onClick={cancelWipeProcess}
                          variant="outline"
                        >
                          İptal
                        </Button>
                      </div>
                    </div>
                  )}
                </div>
              ) : (
                /* İşlem Durumu */
                <div className="space-y-6 text-center">
                  <div>
                    <h4 className="text-lg font-medium mb-2">Temizleme İşlemi Devam Ediyor</h4>
                    <p className="text-muted-foreground">
                      Lütfen bekleyin, cihazınız temizleniyor...
                    </p>
                  </div>

                  <div className="space-y-2">
                    <Progress value={wipeProgress} className="w-full" />
                    <p className="text-sm text-muted-foreground">
                      %{wipeProgress} tamamlandı
                    </p>
                  </div>

                  <div className="space-y-2">
                    <h5 className="font-medium">İşlenen Alanlar:</h5>
                    <div className="text-sm space-y-1">
                      {selected.map((option, index) => {
                        const isCompleted = wipeProgress > (index / selected.length) * 100
                        const isProcessing = wipeProgress >= (index / selected.length) * 100 && 
                                           wipeProgress < ((index + 1) / selected.length) * 100
                        
                        return (
                          <div key={option.id} className="flex items-center justify-between">
                            <span>{option.title}</span>
                            <Badge variant={
                              isCompleted ? 'default' : 
                              isProcessing ? 'secondary' : 
                              'outline'
                            }>
                              {isCompleted ? 'Tamamlandı' : 
                               isProcessing ? 'İşleniyor' : 
                               'Beklemede'}
                            </Badge>
                          </div>
                        )
                      })}
                    </div>
                  </div>

                  <Alert>
                    <AlertDescription>
                      İşlem sırasında cihazı kapatmayın veya bağlantıyı kesmeyin!
                    </AlertDescription>
                  </Alert>
                </div>
              )}
            </DialogContent>
          </Dialog>

          {/* Hızlı Temizleme Seçenekleri */}
          <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
            <Card>
              <CardHeader>
                <CardTitle className="text-base">Hızlı Önbellek Temizleme</CardTitle>
                <CardDescription>
                  Sadece önbellek ve geçici dosyaları temizler
                </CardDescription>
              </CardHeader>
              <CardContent>
                <Button 
                  variant="outline" 
                  disabled={!isConnected}
                  onClick={() => {
                    setSelectedWipeOptions(['cache'])
                    setIsWipeDialogOpen(true)
                  }}
                  className="w-full"
                >
                  Önbellek Temizle
                </Button>
              </CardContent>
            </Card>

            <Card>
              <CardHeader>
                <CardTitle className="text-base">Tam Fabrika Sıfırlaması</CardTitle>
                <CardDescription>
                  Cihazı tamamen fabrika ayarlarına döndürür
                </CardDescription>
              </CardHeader>
              <CardContent>
                <Button 
                  variant="destructive" 
                  disabled={!isConnected}
                  onClick={() => {
                    setSelectedWipeOptions(['factory'])
                    setIsWipeDialogOpen(true)
                  }}
                  className="w-full"
                >
                  Fabrika Sıfırlaması
                </Button>
              </CardContent>
            </Card>
          </div>

          {/* Güvenlik Bilgileri */}
          <Card>
            <CardHeader>
              <CardTitle className="text-base">Güvenlik Bilgileri</CardTitle>
            </CardHeader>
            <CardContent className="space-y-2 text-sm">
              <div className="flex items-start gap-2">
                <Badge variant="outline">1</Badge>
                <p>Temizleme işlemleri geri alınamaz. Önemli verilerinizi yedekleyin.</p>
              </div>
              <div className="flex items-start gap-2">
                <Badge variant="outline">2</Badge>
                <p>Fabrika sıfırlaması cihazı tamamen temizler ve yeniden kurulum gerektirir.</p>
              </div>
              <div className="flex items-start gap-2">
                <Badge variant="outline">3</Badge>
                <p>İşlem sırasında cihaz bağlantısını kesmeyin.</p>
              </div>
              <div className="flex items-start gap-2">
                <Badge variant="outline">4</Badge>
                <p>Temizleme sonrası cihaz yeniden başlatılabilir.</p>
              </div>
            </CardContent>
          </Card>
        </CardContent>
      </Card>
    </div>
  )
}
