'use client'

import { useState, useEffect } from 'react'
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card'
import { Button } from '@/components/ui/button'
import { Input } from '@/components/ui/input'
import { Label } from '@/components/ui/label'
import { Badge } from '@/components/ui/badge'
import { Alert, AlertDescription } from '@/components/ui/alert'
import { Dialog, DialogContent, DialogDescription, DialogHeader, DialogTitle, DialogTrigger } from '@/components/ui/dialog'
import { Tabs, TabsContent, TabsList, TabsTrigger } from '@/components/ui/tabs'
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select'

interface MediaFile {
  id: string
  name: string
  type: 'image' | 'video'
  size: number
  path: string
  thumbnail: string
  dateCreated: string
  dateModified: string
  resolution?: string
  duration?: number
  selected: boolean
}

interface GalleryViewerProps {
  deviceId: string
  isConnected: boolean
}

export default function GalleryViewer({ deviceId, isConnected }: GalleryViewerProps) {
  const [mediaFiles, setMediaFiles] = useState<MediaFile[]>([])
  const [isLoading, setIsLoading] = useState(false)
  const [error, setError] = useState('')
  const [searchTerm, setSearchTerm] = useState('')
  const [selectedFile, setSelectedFile] = useState<MediaFile | null>(null)
  const [viewMode, setViewMode] = useState<'grid' | 'list'>('grid')
  const [filterType, setFilterType] = useState<'all' | 'image' | 'video'>('all')
  const [sortBy, setSortBy] = useState<'name' | 'date' | 'size'>('date')
  const [selectedFiles, setSelectedFiles] = useState<string[]>([])
  const [isDownloading, setIsDownloading] = useState(false)

  // Simüle medya dosyaları
  const generateSampleMedia = (): MediaFile[] => {
    const sampleFiles: MediaFile[] = []
    
    // Örnek resimler
    for (let i = 1; i <= 15; i++) {
      sampleFiles.push({
        id: `img_${i}`,
        name: `IMG_${String(i).padStart(4, '0')}.jpg`,
        type: 'image',
        size: Math.floor(Math.random() * 5000000) + 500000, // 0.5-5MB
        path: `/storage/emulated/0/DCIM/Camera/IMG_${String(i).padStart(4, '0')}.jpg`,
        thumbnail: `data:image/svg+xml;base64,${btoa(`<svg width="200" height="200" xmlns="http://www.w3.org/2000/svg"><rect width="200" height="200" fill="hsl(${i * 25}, 70%, 60%)"/><text x="100" y="100" text-anchor="middle" dy=".3em" fill="white" font-size="14">IMG ${i}</text></svg>`)}`,
        dateCreated: new Date(Date.now() - Math.random() * 30 * 24 * 60 * 60 * 1000).toLocaleString('tr-TR'),
        dateModified: new Date(Date.now() - Math.random() * 30 * 24 * 60 * 60 * 1000).toLocaleString('tr-TR'),
        resolution: `${1920 + Math.floor(Math.random() * 1080)}x${1080 + Math.floor(Math.random() * 920)}`,
        selected: false
      })
    }
    
    // Örnek videolar
    for (let i = 1; i <= 8; i++) {
      sampleFiles.push({
        id: `vid_${i}`,
        name: `VID_${String(i).padStart(4, '0')}.mp4`,
        type: 'video',
        size: Math.floor(Math.random() * 50000000) + 10000000, // 10-60MB
        path: `/storage/emulated/0/DCIM/Camera/VID_${String(i).padStart(4, '0')}.mp4`,
        thumbnail: `data:image/svg+xml;base64,${btoa(`<svg width="200" height="200" xmlns="http://www.w3.org/2000/svg"><rect width="200" height="200" fill="hsl(${i * 45}, 50%, 40%)"/><polygon points="80,70 80,130 130,100" fill="white"/><text x="100" y="150" text-anchor="middle" dy=".3em" fill="white" font-size="12">VID ${i}</text></svg>`)}`,
        dateCreated: new Date(Date.now() - Math.random() * 30 * 24 * 60 * 60 * 1000).toLocaleString('tr-TR'),
        dateModified: new Date(Date.now() - Math.random() * 30 * 24 * 60 * 60 * 1000).toLocaleString('tr-TR'),
        resolution: `${1920}x${1080}`,
        duration: Math.floor(Math.random() * 300) + 30, // 30-330 saniye
        selected: false
      })
    }
    
    return sampleFiles.sort((a, b) => new Date(b.dateCreated).getTime() - new Date(a.dateCreated).getTime())
  }

  const loadGallery = async () => {
    if (!isConnected) {
      setError('Cihaz bağlı değil')
      return
    }

    setIsLoading(true)
    setError('')

    try {
      // Simüle galeri yükleme
      await new Promise(resolve => setTimeout(resolve, 2000))
      
      const sampleMedia = generateSampleMedia()
      setMediaFiles(sampleMedia)
      
    } catch (err) {
      setError('Galeri yüklenemedi')
    } finally {
      setIsLoading(false)
    }
  }

  const downloadFile = async (file: MediaFile) => {
    try {
      setIsDownloading(true)
      
      // Simüle dosya indirme
      await new Promise(resolve => setTimeout(resolve, 2000))
      
      // Gerçek uygulamada burada dosya indirilecek
      const link = document.createElement('a')
      link.download = file.name
      link.href = file.thumbnail // Simülasyon için thumbnail kullanıyoruz
      link.click()
      
    } catch (err) {
      setError('Dosya indirilemedi')
    } finally {
      setIsDownloading(false)
    }
  }

  const downloadSelectedFiles = async () => {
    if (selectedFiles.length === 0) {
      setError('İndirilecek dosya seçin')
      return
    }

    try {
      setIsDownloading(true)
      
      // Simüle toplu indirme
      await new Promise(resolve => setTimeout(resolve, 3000))
      
      // Gerçek uygulamada burada seçili dosyalar indirilecek
      alert(`${selectedFiles.length} dosya indirildi`)
      setSelectedFiles([])
      
    } catch (err) {
      setError('Dosyalar indirilemedi')
    } finally {
      setIsDownloading(false)
    }
  }

  const deleteFile = async (fileId: string) => {
    try {
      // Simüle dosya silme
      await new Promise(resolve => setTimeout(resolve, 1000))
      
      setMediaFiles(prev => prev.filter(file => file.id !== fileId))
      setSelectedFile(null)
      setSelectedFiles(prev => prev.filter(id => id !== fileId))
      
    } catch (err) {
      setError('Dosya silinemedi')
    }
  }

  const deleteSelectedFiles = async () => {
    if (selectedFiles.length === 0) {
      setError('Silinecek dosya seçin')
      return
    }

    try {
      // Simüle toplu silme
      await new Promise(resolve => setTimeout(resolve, 1500))
      
      setMediaFiles(prev => prev.filter(file => !selectedFiles.includes(file.id)))
      setSelectedFiles([])
      setSelectedFile(null)
      
    } catch (err) {
      setError('Dosyalar silinemedi')
    }
  }

  const toggleFileSelection = (fileId: string) => {
    setSelectedFiles(prev => 
      prev.includes(fileId) 
        ? prev.filter(id => id !== fileId)
        : [...prev, fileId]
    )
  }

  const selectAllFiles = () => {
    const allFileIds = filteredAndSortedFiles.map(file => file.id)
    setSelectedFiles(allFileIds)
  }

  const deselectAllFiles = () => {
    setSelectedFiles([])
  }

  const formatFileSize = (bytes: number): string => {
    if (bytes === 0) return '0 B'
    const k = 1024
    const sizes = ['B', 'KB', 'MB', 'GB']
    const i = Math.floor(Math.log(bytes) / Math.log(k))
    return parseFloat((bytes / Math.pow(k, i)).toFixed(2)) + ' ' + sizes[i]
  }

  const formatDuration = (seconds: number): string => {
    const mins = Math.floor(seconds / 60)
    const secs = seconds % 60
    return `${mins}:${secs.toString().padStart(2, '0')}`
  }

  const filteredAndSortedFiles = mediaFiles
    .filter(file => {
      const matchesSearch = file.name.toLowerCase().includes(searchTerm.toLowerCase())
      const matchesType = filterType === 'all' || file.type === filterType
      return matchesSearch && matchesType
    })
    .sort((a, b) => {
      switch (sortBy) {
        case 'name':
          return a.name.localeCompare(b.name)
        case 'size':
          return b.size - a.size
        case 'date':
        default:
          return new Date(b.dateCreated).getTime() - new Date(a.dateCreated).getTime()
      }
    })

  useEffect(() => {
    if (isConnected && mediaFiles.length === 0) {
      loadGallery()
    }
  }, [isConnected])

  return (
    <div className="space-y-6">
      <Card>
        <CardHeader>
          <CardTitle>Galeri Yönetimi</CardTitle>
          <CardDescription>
            Cihazınızdaki fotoğraf ve videoları görüntüleyin, indirin ve yönetin
          </CardDescription>
        </CardHeader>
        <CardContent className="space-y-4">
          {/* Kontrol Butonları */}
          <div className="flex flex-wrap gap-2">
            <Button 
              onClick={loadGallery}
              disabled={!isConnected || isLoading}
            >
              {isLoading ? 'Yükleniyor...' : 'Galeriyi Yenile'}
            </Button>
            
            <Button
              variant="outline"
              onClick={downloadSelectedFiles}
              disabled={selectedFiles.length === 0 || isDownloading}
            >
              {isDownloading ? 'İndiriliyor...' : `Seçilenleri İndir (${selectedFiles.length})`}
            </Button>
            
            <Button
              variant="outline"
              onClick={deleteSelectedFiles}
              disabled={selectedFiles.length === 0}
            >
              Seçilenleri Sil ({selectedFiles.length})
            </Button>
          </div>

          {error && (
            <Alert>
              <AlertDescription>{error}</AlertDescription>
            </Alert>
          )}

          {/* Filtreler ve Arama */}
          <div className="grid grid-cols-1 md:grid-cols-4 gap-4">
            <div className="space-y-2">
              <Label htmlFor="search">Ara</Label>
              <Input
                id="search"
                placeholder="Dosya adı..."
                value={searchTerm}
                onChange={(e) => setSearchTerm(e.target.value)}
              />
            </div>
            
            <div className="space-y-2">
              <Label>Tür</Label>
              <Select value={filterType} onValueChange={(value: any) => setFilterType(value)}>
                <SelectTrigger>
                  <SelectValue />
                </SelectTrigger>
                <SelectContent>
                  <SelectItem value="all">Tümü</SelectItem>
                  <SelectItem value="image">Resimler</SelectItem>
                  <SelectItem value="video">Videolar</SelectItem>
                </SelectContent>
              </Select>
            </div>
            
            <div className="space-y-2">
              <Label>Sırala</Label>
              <Select value={sortBy} onValueChange={(value: any) => setSortBy(value)}>
                <SelectTrigger>
                  <SelectValue />
                </SelectTrigger>
                <SelectContent>
                  <SelectItem value="date">Tarihe Göre</SelectItem>
                  <SelectItem value="name">Ada Göre</SelectItem>
                  <SelectItem value="size">Boyuta Göre</SelectItem>
                </SelectContent>
              </Select>
            </div>
            
            <div className="space-y-2">
              <Label>Görünüm</Label>
              <div className="flex gap-2">
                <Button
                  size="sm"
                  variant={viewMode === 'grid' ? 'default' : 'outline'}
                  onClick={() => setViewMode('grid')}
                >
                  Izgara
                </Button>
                <Button
                  size="sm"
                  variant={viewMode === 'list' ? 'default' : 'outline'}
                  onClick={() => setViewMode('list')}
                >
                  Liste
                </Button>
              </div>
            </div>
          </div>

          {/* Seçim Kontrolleri */}
          {filteredAndSortedFiles.length > 0 && (
            <div className="flex items-center justify-between p-2 border rounded">
              <div className="flex items-center gap-2">
                <span className="text-sm">
                  {filteredAndSortedFiles.length} dosya
                  {selectedFiles.length > 0 && ` (${selectedFiles.length} seçili)`}
                </span>
              </div>
              <div className="flex gap-2">
                <Button size="sm" variant="outline" onClick={selectAllFiles}>
                  Tümünü Seç
                </Button>
                <Button size="sm" variant="outline" onClick={deselectAllFiles}>
                  Seçimi Temizle
                </Button>
              </div>
            </div>
          )}
        </CardContent>
      </Card>

      {/* Medya Dosyaları */}
      <div className="grid grid-cols-1 lg:grid-cols-3 gap-6">
        {/* Dosya Listesi/Izgara */}
        <div className="lg:col-span-2">
          <Card>
            <CardHeader>
              <CardTitle>
                Medya Dosyaları
                <Badge variant="secondary" className="ml-2">
                  {filteredAndSortedFiles.length}
                </Badge>
              </CardTitle>
            </CardHeader>
            <CardContent>
              {filteredAndSortedFiles.length === 0 ? (
                <div className="text-center py-8">
                  <p className="text-muted-foreground">
                    {mediaFiles.length === 0 ? 'Henüz medya dosyası yok' : 'Arama sonucu bulunamadı'}
                  </p>
                </div>
              ) : viewMode === 'grid' ? (
                <div className="grid grid-cols-2 md:grid-cols-3 lg:grid-cols-4 gap-4">
                  {filteredAndSortedFiles.map((file) => (
                    <div key={file.id} className="relative group">
                      <div 
                        className="aspect-square border rounded-lg overflow-hidden cursor-pointer hover:shadow-md transition-shadow"
                        onClick={() => setSelectedFile(file)}
                      >
                        <img 
                          src={file.thumbnail} 
                          alt={file.name}
                          className="w-full h-full object-cover"
                        />
                        
                        {/* Dosya Türü Badge */}
                        <div className="absolute top-2 left-2">
                          <Badge variant={file.type === 'video' ? 'default' : 'secondary'}>
                            {file.type === 'video' ? 'Video' : 'Resim'}
                          </Badge>
                        </div>
                        
                        {/* Video Süresi */}
                        {file.type === 'video' && file.duration && (
                          <div className="absolute bottom-2 right-2">
                            <Badge variant="secondary">
                              {formatDuration(file.duration)}
                            </Badge>
                          </div>
                        )}
                        
                        {/* Seçim Checkbox */}
                        <div className="absolute top-2 right-2">
                          <input
                            type="checkbox"
                            checked={selectedFiles.includes(file.id)}
                            onChange={(e) => {
                              e.stopPropagation()
                              toggleFileSelection(file.id)
                            }}
                            className="w-4 h-4"
                          />
                        </div>
                      </div>
                      
                      <div className="mt-2">
                        <p className="text-sm font-medium truncate">{file.name}</p>
                        <p className="text-xs text-muted-foreground">
                          {formatFileSize(file.size)}
                        </p>
                      </div>
                    </div>
                  ))}
                </div>
              ) : (
                <div className="space-y-2 max-h-96 overflow-y-auto">
                  {filteredAndSortedFiles.map((file) => (
                    <div 
                      key={file.id}
                      className="flex items-center gap-3 p-3 border rounded-lg cursor-pointer hover:bg-muted/50"
                      onClick={() => setSelectedFile(file)}
                    >
                      <input
                        type="checkbox"
                        checked={selectedFiles.includes(file.id)}
                        onChange={(e) => {
                          e.stopPropagation()
                          toggleFileSelection(file.id)
                        }}
                        className="w-4 h-4"
                      />
                      
                      <img 
                        src={file.thumbnail} 
                        alt={file.name}
                        className="w-12 h-12 object-cover rounded"
                      />
                      
                      <div className="flex-1">
                        <p className="font-medium">{file.name}</p>
                        <div className="flex items-center gap-4 text-sm text-muted-foreground">
                          <span>{formatFileSize(file.size)}</span>
                          <span>{file.resolution}</span>
                          {file.duration && <span>{formatDuration(file.duration)}</span>}
                        </div>
                      </div>
                      
                      <Badge variant={file.type === 'video' ? 'default' : 'secondary'}>
                        {file.type === 'video' ? 'Video' : 'Resim'}
                      </Badge>
                    </div>
                  ))}
                </div>
              )}
            </CardContent>
          </Card>
        </div>

        {/* Dosya Detayları */}
        <div className="lg:col-span-1">
          <Card>
            <CardHeader>
              <CardTitle>Dosya Detayları</CardTitle>
            </CardHeader>
            <CardContent>
              {!selectedFile ? (
                <div className="text-center py-8">
                  <p className="text-muted-foreground">
                    Detayları görmek için bir dosya seçin
                  </p>
                </div>
              ) : (
                <div className="space-y-4">
                  {/* Önizleme */}
                  <div className="aspect-square border rounded-lg overflow-hidden">
                    <img 
                      src={selectedFile.thumbnail} 
                      alt={selectedFile.name}
                      className="w-full h-full object-cover"
                    />
                  </div>
                  
                  {/* Dosya Bilgileri */}
                  <div className="space-y-2">
                    <h4 className="font-medium">{selectedFile.name}</h4>
                    
                    <div className="text-sm space-y-1">
                      <p><strong>Tür:</strong> {selectedFile.type === 'video' ? 'Video' : 'Resim'}</p>
                      <p><strong>Boyut:</strong> {formatFileSize(selectedFile.size)}</p>
                      <p><strong>Çözünürlük:</strong> {selectedFile.resolution}</p>
                      {selectedFile.duration && (
                        <p><strong>Süre:</strong> {formatDuration(selectedFile.duration)}</p>
                      )}
                      <p><strong>Oluşturulma:</strong> {selectedFile.dateCreated}</p>
                      <p><strong>Değiştirilme:</strong> {selectedFile.dateModified}</p>
                    </div>
                    
                    <div className="text-xs text-muted-foreground">
                      <p><strong>Yol:</strong> {selectedFile.path}</p>
                    </div>
                  </div>
                  
                  {/* İşlemler */}
                  <div className="space-y-2">
                    <Button 
                      onClick={() => downloadFile(selectedFile)}
                      disabled={isDownloading}
                      className="w-full"
                    >
                      {isDownloading ? 'İndiriliyor...' : 'İndir'}
                    </Button>
                    
                    <Button
                      variant="outline"
                      onClick={() => deleteFile(selectedFile.id)}
                      className="w-full"
                    >
                      Sil
                    </Button>
                    
                    <Button
                      variant="outline"
                      onClick={() => toggleFileSelection(selectedFile.id)}
                      className="w-full"
                    >
                      {selectedFiles.includes(selectedFile.id) ? 'Seçimi Kaldır' : 'Seç'}
                    </Button>
                  </div>
                </div>
              )}
            </CardContent>
          </Card>
        </div>
      </div>
    </div>
  )
}
